import {Injectable} from "@angular/core";
import {envConfig} from "./environment";
import {VojConfiguration} from "../models/voj-configuration.model";
import {HttpRequestProvider} from "../providers/http-request/http-request";
import {VojTuple} from "../models/voj-tuple.model";
import {EventBusProvider} from "../providers/voj-events/voj-events";

@Injectable()
export class Configuration {
    configuration : VojConfiguration;

    constructor(public http: HttpRequestProvider,
                private eventBus : EventBusProvider) {
      this.configuration = new VojConfiguration();
    }

    initialize() {
        let payload = {
          "cid":envConfig.cid
        };

        return this.http
            .call(payload, envConfig.server, "", true)
            .do((data:any)=>{
                if(data && data.length > 0){
                    this.configuration.brand = data[0].brand;
                    this.configuration.customer = data[0].customer;
                    this.configuration.env = data[0].env;
                    this.configuration.target = data[0].target;
                    this.configuration.dlMode = data[0].dlmode;
                    this.configuration.logo = data[0].logo;
                    this.configuration.brand = data[0].brand;
                    this.configuration.css = data[0].css;
                    this.configuration.urls = [];
                    for(let key in data[0].urls){
                        let t = new VojTuple();
                        t.key = key;
                        t.value = data[0].urls[key];
                        this.configuration.urls.push(t);
                    }

                    this.eventBus.publishAppHeader();
                }
            })
            .map(() => this.configuration);

    }

}


