import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import {Storage} from "@ionic/storage";
import {VojPageHeaderType} from "../../../models/voj-page-header.model";
import {MissionServiceProvider} from "../../../providers/mission-service/mission-service";
import {VojCardModel} from "../../../models/voj-card.model";
import {SearchHeaderData} from "../../../models/search-header.model";
import {SearchQuery} from "../../../models/search-query.model";
import {TextType} from "../../../models/text-type";
import {DateUtils} from "../../../utils/utils";
import {ButtonType} from "../../../models/button-type.model";
import {NavigationService} from "../../../services/navigation.service";
import {VojPage} from "../../../models/voj-page";
import {MissionSignaturePage} from "../mission-signature/mission-signature";

@IonicPage()
@Component({
  selector: 'page-pending-missions',
  templateUrl: 'pending-missions.html',
})
export class PendingMissionsPage {
  pageHeader : VojPageHeaderType;
  searchHeaderData : SearchHeaderData;
  cards : Array<VojCardModel>;
  mobileStyle: any;
  searchType: boolean;
  searchQuery : SearchQuery;
  timer : any;
  interval : number = 10000;

  constructor(public navCtrl: NavController,
              public missionService : MissionServiceProvider,
              public storage : Storage,
              public navService : NavigationService,
              public navParams: NavParams) {
    this.pageHeader = new VojPageHeaderType();
    this.pageHeader.actions = [];
    this.pageHeader.title = 'Contrat(s)  signer';

    this.searchHeaderData = new SearchHeaderData();
    this.searchHeaderData.action = (q: SearchQuery) => {
      this.performSearch(q);
    };
    this.searchHeaderData.placeholders = {
      main: 'Rechercher dans mes missions',
      what: 'Job',
      where: 'Numro de contrat',
      when: 'Date',
      who: 'Tiers'
    };

    this.cards = this.missionService.pendingContractsCards;
    for(let i = 0 ; i < this.cards.length ; i++) {
      this.cards[i].actions[0].action = ()=> {
        this.gotoSigning(this.cards[i]);
      };
      this.cards[i].actions[1].action = ()=> {
        this.refuseSigning(this.cards[i]);
      };
    }

  }

  performSearch(query : SearchQuery) {
    this.searchQuery = query;
    this.storage.get("currentUser").then((sdata: any) => {
      let ac = JSON.parse(sdata);
      this.missionService.loadMissions(ac.id, 0, 'pending_contracts_search',this.searchQuery).then((data : any)=>{
        this.cards = [];
        for(let i = 0 ; i < data.length ; i++) {
          let o = data[i];
          let c : VojCardModel = new VojCardModel();
          c.id = o.id;
          c.title = null;
          c.subtitles = [];
          let sb : TextType;

          sb = new TextType();
          sb.label = 'Du '+DateUtils.sqlToDisplay(o.start)+' au '+DateUtils.sqlToDisplay(o.end);
          c.subtitles.push(sb);
          sb = new TextType();
          sb.label = o.title;
          c.subtitles.push(sb);
          sb = new TextType();
          sb.label = o.enterprise.name;
          c.subtitles.push(sb);
          sb = new TextType();
          sb.label = 'Contrat n'+o.number;
          c.subtitles.push(sb);

          c.actions = [<ButtonType>{
            id : 'consult_sign',
            icon:'',
            label:"Consulter avant signature",
            action:()=> {
              this.gotoSigning(this.cards[i]);
            },
            style : {'background-color' : '#eca613', 'color' : '#ffffff'}
          },
            <ButtonType>{
              id : 'refuse',
              icon:'',
              label:"Refuser",
              action:()=> {
                this.refuseSigning(this.cards[i]);
              },
              style : {'background-color' : '#ff0000', 'color' : '#ffffff'}
            }];

          this.cards.push(c);
        }

      });
    });
  }

  handleSearchType(searchType){
    this.searchType = searchType;
  }

  gotoSigning(c){
    this.missionService.loadContractLink(c.id).then((data:any)=>{
      let navData = {link : data.link, idContract : c.id};
      let page : VojPage = <VojPage>{
        page: MissionSignaturePage,
        params:navData,
        isRoot: true
      };
      this.navService.navigate("main", page);

      this.timer = setInterval(()=> {
        this.missionService.checkSignature(c.id).then((data : any)=> {
          let status = data.status;
          if(status) {
            clearInterval(this.timer);
            this.searchQuery = new SearchQuery();
            this.performSearch(this.searchQuery);
          }

        });

      },this.interval);
    });
  }

  refuseSigning(c){
    this.missionService.cancelContract(c.id).then(data=>{
      this.storage.get('currentUser').then((sdata)=>{
        let data = JSON.parse(sdata);
        let idt : number = data.id;
        this.missionService.pendingMissions(idt);
      });
      let index = -1;
      for(let i = 0 ; i < this.cards.length ; i++){
        if(this.cards[i].id == c.id){
          index = i;
          break;
        }
      }

      this.cards.splice(index, 1);

    });

  }

  ionViewDidLoad() {

  }

}



