import { Component } from '@angular/core';
import { Platform } from 'ionic-angular';
import {Storage} from "@ionic/storage";
import { Configuration } from '../../../config/voj-configuration';
import { FormType } from '../../../models/form-type.model';
import { VojPageHeaderType } from '../../../models/voj-page-header.model';
import { VojConfiguration } from '../../../models/voj-configuration.model';
import { AvailabilitiesPage } from '../availabilities/availabilities';
import {CvServiceProvider} from "../../../providers/cv-services/cv-service/cv-service";
import {NavigationService} from "../../../services/navigation.service";
import {VojPage} from "../../../models/voj-page";

// Availabilities 3/4
@Component({
    selector: 'availabilities-add-days-page',
    templateUrl: 'availabilities-add-days.html'
})
export class AvailabilitiesAddDaysPage {
    config: VojConfiguration;
    headerData: VojPageHeaderType;
    formData: FormType;

    constructor(public configurationService: Configuration,
                public cvService : CvServiceProvider,
                public navigationService : NavigationService,
                public storage : Storage,
                public platform: Platform) {
        this.config = this.configurationService.configuration;

        this.headerData = <VojPageHeaderType>{
            title: 'AVAILABILITIES_ADD_DAYS.ADD_AVAILABILITY',
            titleLocalized: true,
            style: 'any',
            actions: [{
                id: 'xdcfgvyhubijnklbhvg',
                label: 'help',
                icon: 'help',
                action: function () {
                    console.log('button works')
                }
            }],
            isCanceled: true
        };

        this.formData = <FormType>{
            fields: [
                {
                    type: 'checkbox',
                    label: "AVAILABILITIES_ADD_DAYS.MONDAY",
                    labelLocalized: true,
                    value: false,
                    visible: true,
                    id: 'monday',
                },
                {
                    type: 'checkbox',
                    label: "AVAILABILITIES_ADD_DAYS.TUESDAY",
                    labelLocalized: true,
                    value: false,
                    visible: true,
                    id: 'tuesday',
                },
                {
                    type: 'checkbox',
                    label: "AVAILABILITIES_ADD_DAYS.WEDNESDAY",
                    labelLocalized: true,
                    value: false,
                    visible: true,
                    id: 'wednesday',
                },
                {
                    type: 'checkbox',
                    label: "AVAILABILITIES_ADD_DAYS.THURSDAY",
                    labelLocalized: true,
                    value: false,
                    visible: true,
                    id: 'thursday',
                },
                {
                    type: 'checkbox',
                    label: "AVAILABILITIES_ADD_DAYS.FRIDAY",
                    labelLocalized: true,
                    value: false,
                    visible: true,
                    id: 'friday',
                },
                {
                    type: 'checkbox',
                    label: "AVAILABILITIES_ADD_DAYS.SATURDAY",
                    labelLocalized: true,
                    value: false,
                    visible: true,
                    id: 'saturday',
                },
                {
                    type: 'checkbox',
                    label: "AVAILABILITIES_ADD_DAYS.SUNDAY",
                    labelLocalized: true,
                    value: false,
                    visible: true,
                    id: 'sunday',
                }
            ],
            submit: {
                id: 'xdcfgvyhubijnkfwlbhvg',
                label: 'AVAILABILITIES_ADD_DAYS.VALIDATE',
                labelLocalized: true,
                icon: 'help',
                action: () => this.addDays()
            }
        }
    }

    addDays() {
        let full : boolean = true;
        let days : Array<string> = [];
        for(let i = 0 ; i < this.formData.fields.length ; i++) {
            if(this.formData.fields[i].value) {
               days.push(i+'');
            } else {
                full = false;
            }
        }
        if(full) {
            days = [];
            days.push('*');
        }

        this.storage.get("currentUser").then((sdata : string)=>{
            let data = JSON.parse(sdata);
            this.cvService.saveFullAvailability(data.id, days).then(data=>{
                let page : VojPage = <VojPage>{page : AvailabilitiesPage, isRoot : true, isWebModal : false};
                this.navigationService.navigate('details', page);
            });
        });

    }

    ionViewDidLoad() {
        console.log('ionViewDidLoad BankSettingsPage');
    }

}



