import { Injectable } from '@angular/core';
import { Observable } from 'rxjs/Observable';

import { File } from '@ionic-native/file';
import { ImagePicker, ImagePickerOptions } from '@ionic-native/image-picker';
import { ImageResizer, ImageResizerOptions } from '@ionic-native/image-resizer';
import { Camera, CameraOptions } from '@ionic-native/camera'

import filenameRegex from 'filename-regex';
import dirnameRegex from 'dirname-regex';
//
export interface ImageBlob {
  name: string;
  blob: string;
}

export interface ImagePath {
  name: string;
  dir: string;
  path: string;
};

@Injectable()
export class NativeProvider {
  constructor(
    private camera: Camera,
    private file: File,
    private imagePicker: ImagePicker,
    private imageResizer: ImageResizer
  ) { }

  private extractSingleImage(images: string | Array<string>): Promise<ImagePath> {
    return new Promise((resolve, reject) => {
      let imagePath: string = images instanceof Array ? images[0] : images;

      let filename: string = imagePath.match(filenameRegex())[0];
      let dirname: string = imagePath.match(dirnameRegex())[1];

      if (filename == null || dirname == null) {
        reject(new ReferenceError('Invalid image\'s path'));
      } else {
        resolve({
          name: filename,
          dir: dirname,
          path: imagePath
        });
      }
    });
  }

  private resizePicture(
    image: ImagePath,
    resizerOptions?: ImageResizerOptions
  ): Promise<any> {
    const options: ImageResizerOptions = resizerOptions || {
      uri: image.path,
      folderName: 'pictures',
      width: 1024,
      height: 1024,
      quality: 90
    };

    return this.imageResizer.resize(options)
  }

  private readAsDataUrl(image: ImagePath): Promise<ImageBlob> {
    return this.file
      .readAsDataURL(image.dir, image.name)
      .then(blob => ({ blob, name: image.name }));
  }

  public pickImage(options: ImagePickerOptions = {}) {
    const imagePickerOptions: ImagePickerOptions = options || { maximumImagesCount: 1 };

    return this.imagePicker
      .hasReadPermission()
      .then(hasPermissions => hasPermissions
        ? Promise.resolve()
        : this.imagePicker.requestReadPermission())
      .then(() => this.imagePicker.hasReadPermission())
      .then(permissionsGranted => {
        if (permissionsGranted) {
          return this.imagePicker
            .getPictures(imagePickerOptions)
            .then(this.extractSingleImage.bind(this))
            .then(this.resizePicture.bind(this))
            .then(this.extractSingleImage.bind(this))
            .then(this.readAsDataUrl.bind(this));
        } else {
          throw new Error('Permissions dismissed');
        }
      });
  }

  public takePicture() {
    const options: CameraOptions = {
      quality: 100,
      destinationType: this.camera.DestinationType.FILE_URI,
      encodingType: this.camera.EncodingType.JPEG,
      mediaType: this.camera.MediaType.PICTURE
    }
    return this.camera
      .getPicture()
      .then(this.extractSingleImage.bind(this))
      .then(this.resizePicture.bind(this))
      .then(this.extractSingleImage.bind(this))
      .then(this.readAsDataUrl.bind(this));
  }
}



