import {ObjectUtils} from "../../../utils/utils";
import {MunicipalityDto} from "../../../dtos/municipality.dto";
import {FieldValidity} from "../../../models/field-validity";

export class NumSSConstraints{

	constructor() {
	}

	private static checkGender(num: string, title: string) {
		let indicator = num.charAt(0);
		if ((indicator === '1' && title != 'Mme') || (indicator === '2' && title === 'Mme')) {
			return true;
		} else {
			return false;
		}
	}

	private static checkBirthYear(num: string, date: any) {
		if (ObjectUtils.isEmpty(date)) {
			return false
		}
		let indicator = num.charAt(1) + num.charAt(2);

		let birthYear = new Date(date).getFullYear().toString().substring(2);

		if (indicator == birthYear)
            return true;
         else
            return false;
	}

	private static checkBirthMonth(num: string, date: any) {
		if (ObjectUtils.isEmpty(date)) {
			return false
		}
		let indicator = num.charAt(3) + num.charAt(4);

		let birthMonth = (new Date(date).getMonth() + 1).toString();

		if (birthMonth.length == 1)
			birthMonth = "0" + birthMonth;

		if (indicator == birthMonth)
            return true;
		else
            return false;
	}

	private static checkINSEE(num: string, municipalityDto: MunicipalityDto) {
		let indicator = num.substring(5, 10);

		if (municipalityDto.id != 0) {
			if (indicator != municipalityDto.codeINSEE)
                return false;
			else
                return true;
		}

		if (indicator.charAt(0) != '9')
            return false;
		else
			return true;
	}

	private static checkModKey(num: string) {
		try {
			let indicator = num.substr(0, 13);
			let key = num.substr(13);
			let number = parseInt(indicator);
			let nkey = parseInt(key);
			let modulo = number % 97;
			if (nkey == 97 - modulo)
                return true;
			else
                return false;
		}
		catch (err) {
			return false;
		}
	}

	public static checkNumss(_numSS, title, birthdate, municipalityDto: MunicipalityDto) {
		let _isValid: boolean = true;
		let _hint: string = "";

		if (_numSS.length != 0 && _numSS.length != 15) {
			_hint = "* Veuillez saisir les 15 chiffres du n SS";
			_isValid = false;
		}
		else if (_numSS.length == 15) {
			let personalInfoHint = "* Le numro de scurit sociale renseign ne correspond pas aux informations personnelles : ";
			if (_numSS.length == 15 && !NumSSConstraints.checkGender(_numSS, title)) {
				personalInfoHint += "Civilit";
				_hint = personalInfoHint;
				_isValid = false;
			}
			else if (_numSS.length == 15 && !NumSSConstraints.checkBirthYear(_numSS, birthdate)) {
				personalInfoHint += "Anne de naissance";
				_hint = personalInfoHint;
				_isValid = false;
			}
			else if (_numSS.length == 15 && !NumSSConstraints.checkBirthMonth(_numSS, birthdate)) {
				personalInfoHint += "Mois de naissance";
				_hint = personalInfoHint;
				_isValid = false;
			}
			else if (_numSS.length == 15 && !NumSSConstraints.checkINSEE(_numSS, municipalityDto)) {
				personalInfoHint += "Numro INSEE";
				_hint = personalInfoHint;
				_isValid = false;
			}
			else if (_numSS.length == 15 && !NumSSConstraints.checkModKey(_numSS)) {
				personalInfoHint += "Cl de validation";
				_hint = personalInfoHint;
				_isValid = false;
			} else {
				_hint = "";
			}
		} else {
			_hint = "";
		}
		let fieldVality: FieldValidity = {isValid: _isValid, hint: _hint};
		return fieldVality;
	}
}


