import {Component} from '@angular/core';
import {Storage} from "@ionic/storage";
import {Configuration} from '../../../config/voj-configuration';
import {JobyerAvailabilityChoicePage} from '../jobyer-availability-choice/jobyer-availability-choice';
import {NavigationService} from '../../../services/navigation.service';
import {VojPageHeaderType} from '../../../models/voj-page-header.model';
import {VojConfiguration} from '../../../models/voj-configuration.model';
import {VojPage} from '../../../models/voj-page';
import {AvailabilityDTO} from "../../../dtos/availabilities.dto";
import {CvServiceProvider} from "../../../providers/cv-services/cv-service/cv-service";
import {VojCalendarEngine} from "../../../providers/cv-services/availabilities-compilation/voj-calendar-engine";
import {VojCalendarModel} from "../../../models/voj-calendar.model";

// Availabilities 1/4
@Component({
    selector: 'availabilities-page',
    templateUrl: 'availabilities.html'
})
export class AvailabilitiesPage {
    config: VojConfiguration;
    headerData: VojPageHeaderType;
    availabilityData : Array<AvailabilityDTO>;

    constructor(public configurationService: Configuration,
                public storage : Storage,
                public cvService : CvServiceProvider,
                public navigationService: NavigationService) {

        this.config = this.configurationService.configuration;

        this.headerData = <VojPageHeaderType>{
            title: 'AVAILABILITIES.MY_AVAILABILITIES',
            titleLocalized: true,
            style: 'any',
            actions: [{
                id: 'help_btn',
                label: 'help',
                icon: 'help',
                action: function () {
                    console.log('button works')
                }
            }],
            isCanceled: true
        };

        this.storage.get("currentUser").then((sdata)=>{
            let data = JSON.parse(sdata);
            let idJobyer = data.id;
            this.cvService.loadAvailabilities(idJobyer).then((data : Array<AvailabilityDTO>)=> {
                this.availabilityData = data;
                this.populateParse();
            });
        });

    }

    populateParse() {
        let vcengine : VojCalendarEngine = new VojCalendarEngine();
        for(let  i=0 ; i < this.availabilityData.length ; i++) {
            let exp : string = this.availabilityData[i].expression;
            let model : VojCalendarModel  = vcengine.parse(exp);
            let displayable : Array<string> = vcengine.display(model);
            if(displayable && displayable.length>0)
                this.availabilityData[i].label = displayable[0];
        }
    }

    deleteAvailability(index) {
        let a : AvailabilityDTO = this.availabilityData[index];
        this.storage.get("currentUser").then((sdata)=> {
            let data = JSON.parse(sdata);
            let idJobyer = data.id;
            this.cvService.deleteAvailability(a.id, idJobyer).then(response=> {
                this.availabilityData.splice(index, 1);
            });
        });
    }

    goToJobyerAvailabilityChoice() {
            let vojNav: VojPage = <VojPage>{page: JobyerAvailabilityChoicePage, isRoot: false};
            this.navigationService.navigate('details', vojNav);
    }

    ionViewDidLoad() {
    }

}



