export class VojCalendarModel {
    type : string;
    daysOfWeek : Array<string>;
    daysOfMonth : Array<string>;
    months : Array<string>;
    years : Array<string>;
    slots : Array<VojCalendarSlots>;

    clone() : VojCalendarModel {
        let c : VojCalendarModel = new VojCalendarModel();
        c.type = this.type;
        c.daysOfWeek = this.daysOfWeek;
        c.daysOfMonth = this.daysOfMonth;
        c.months = this.months;
        c.years = this.years;
        c.slots = [];
        for(let i = 0 ; i < this.slots.length ; i++){
            c.slots.push(<VojCalendarSlots>{
                startDay : this.slots[i].startDay,
                endDay : this.slots[i].endDay,
                startHour : this.slots[i].startHour,
                endHour : this.slots[i].endHour,
            });
        }
        return c;
    }
}

export class VojCalendarSlots {
    startDay : string;
    endDay : string;
    startHour : number;
    endHour : number;
}


