import {VojCalendarCompiler} from "./voj-calendar-compiler";
import {
    SimpleSlotCompiler, WeekSlotCompiler, MonthSlotCompiler,
    FullSlotCompiler
} from "./voj-calendar-compiler-algorithms";
export class VojCalendarFactory {
    create(type : string) : VojCalendarCompiler {
        if(type == "SLOT")
            return new SimpleSlotCompiler();
        if(type == "WEEK_SLOT")
            return new WeekSlotCompiler();
        if(type == "MONTH_SLOT")
            return new MonthSlotCompiler();
        if(type == "FULL")
            return new FullSlotCompiler();
        return null;
    }
}


