import { Component } from '@angular/core';
import { Platform, NavController } from 'ionic-angular';

import { Configuration } from '../../../config/voj-configuration';
import { VojPageHeaderType } from '../../../models/voj-page-header.model';
import { VojConfiguration } from '../../../models/voj-configuration.model';
import { JobsListPage } from '../jobs-list/jobs-list';
import { QualitiesPage } from '../qualities/qualities';
import { LanguagesPage } from '../languages/languages';
import { MyCvPage } from '../my-cv/my-cv';
import { KeywordsPage } from '../keywords/keywords';
import { RemunerationPage } from '../remuneration/remuneration';
import {NavigationService} from "../../../services/navigation.service";
import {AvailabilitiesPage} from "../availabilities/availabilities";
import {MyInterestsPage} from "../my-interests/my-interests";
import { VojPage } from '../../../models/voj-page';
import { Page } from 'ionic-angular/navigation/nav-util';

@Component({
    selector: 'information-resume-page',
    templateUrl: 'information-resume.html',
})
export class InformationResumePage {
    config: VojConfiguration;
    headerData: VojPageHeaderType;
    isMobile: boolean = false;
    informationResumeData: Array<{}>;

    constructor(public configurationService: Configuration,
        public platform: Platform,
        public navCtrl: NavController,
        public navService: NavigationService) {

        platform.ready().then(() => {
            this.config = this.configurationService.configuration;
            this.isMobile = !this.config.isLargeScreen;
        });

        this.headerData = {
            title: 'INFORMATION_RESUME.MY_CV',
            style: 'any',
            actions: [{
                id: 'xdcfgvyhubijnklbhvg',
                label: 'help',
                icon: 'help',
                action: function () {
                    console.log('button works')
                }
            }],
            isCanceled: true
        };

        this.informationResumeData = [
            {
                icon: 'md-create',
                label: 'INFORMATION_RESUME.MY_JOBS',
                action: () => this.navigate(JobsListPage)
            },
            {
                icon: 'md-create',
                label: 'INFORMATION_RESUME.MY_AVAILABILITIES',
                action: () => this.navigate(AvailabilitiesPage)
            },
            {
                icon: 'md-create',
                label: 'INFORMATION_RESUME.MY_REMUNERATION',
                action: () => this.navigate(RemunerationPage)
            },
            {
                icon: 'md-create',
                label: 'INFORMATION_RESUME.MY_QUALITIES',
                action: () => this.navigate(QualitiesPage)
            },
            {
                icon: 'md-create',
                label: 'INFORMATION_RESUME.MY_LANGUAGES',
                action: () => this.navigate(LanguagesPage)
            },
            {
                icon: 'md-create',
                label: 'INFORMATION_RESUME.MY_KEYWORDS',
                action: () => this.navigate(KeywordsPage)
            },
            {
                icon: 'md-create',
                label: 'INFORMATION_RESUME.MY_CV',
                action: () => this.navigate(MyCvPage)
            },
            {
                icon: 'eye',
                label: 'INFORMATION_RESUME.MY_INTERESTS',
                action: () => this.navigate(MyInterestsPage)
            }
        ]

    }

    public navigate(page: Page) {
        const pageObject: VojPage = {
            page,
            isRoot: !this.isMobile,
            hasFullHeightContent: true
        };
        
        this.navService.navigate('details', pageObject);
    }

}



