import {Injectable} from "@angular/core";
import {HttpRequestProvider} from "../http-request/http-request";
import {Configuration} from "../../config/voj-configuration";
import {SearchQuery} from "../../models/search-query.model";
import {MissionServiceFactory} from "./mission-service-factory";
import {MissionServiceStrategy} from "./mission-service-strategy";
import {VojCardModel} from "../../models/voj-card.model";
import {TextType} from "../../models/text-type";
import {DateUtils} from "../../utils/utils";
import {EventBusProvider} from "../voj-events/voj-events";
import {ButtonType} from "../../models/button-type.model";
import {VojTuple} from "../../models/voj-tuple.model";

@Injectable()
export class MissionServiceProvider {
    constructor(public http : HttpRequestProvider,
                private eventBus : EventBusProvider,
                private configuration : Configuration) {

    }

    public pendingContractsCards : Array<VojCardModel> = [];

    checkSignature(idc) {
        let factory : MissionServiceFactory = new MissionServiceFactory(this.http, this.configuration);
        let strategy : MissionServiceStrategy = factory.create();
        return strategy.checkSignature(idc);
    }

    pendingMissions(idTier) {
        let offset : number = 0;
        let mode : string = "pending_contracts";
        let constraints : SearchQuery = new SearchQuery();

        let factory : MissionServiceFactory = new MissionServiceFactory(this.http, this.configuration);
        let strategy : MissionServiceStrategy = factory.create();
        strategy.loadContracts(idTier, offset, mode, constraints).then((data : any)=>{
            this.pendingContractsCards = [];
            for(let i = 0 ; i < data.length ; i++) {
                let o = data[i];
                let c : VojCardModel = new VojCardModel();
                c.id = o.id;
                c.title = null;
                c.subtitles = [];
                let sb : TextType;

                sb = new TextType();
                sb.label = 'Du '+DateUtils.sqlToDisplay(o.start)+' au '+DateUtils.sqlToDisplay(o.end);
                c.subtitles.push(sb);
                sb = new TextType();
                sb.label = o.title;
                c.subtitles.push(sb);
                sb = new TextType();
                sb.label = o.enterprise.name;
                c.subtitles.push(sb);
                sb = new TextType();
                sb.label = 'Contrat n'+o.number;
                c.subtitles.push(sb);

                c.actions = [<ButtonType>{
                    id : 'consult_sign',
                    icon:'',
                    label:"Consulter avant signature",
                    action:()=> {

                    },
                    style : {'background-color' : '#eca613', 'color' : '#ffffff'}
                },
                    <ButtonType>{
                    id : 'refuse',
                    icon:'',
                    label:"Refuser",
                    action:()=> {

                    },
                    style : {'background-color' : '#ff0000', 'color' : '#ffffff'}
                }];

                this.pendingContractsCards.push(c);
            }
            this.eventBus.publishAppEventUpdate('pending_missions_loaded');
        });
    }

    cancelContract(idc : number) {
        let payload = {
            service:"CANCEL_CONTRACT",
            mission_id:idc
        };




        let url = "";
        for (let i = 0; i < this.configuration.configuration.urls.length; i++) {
            let t : VojTuple = this.configuration.configuration.urls[i];
            if (t.key == "contract") {
                url = t.value;
                break;
            }
        }

        console.log(JSON.stringify(payload));

        return new Promise(resolve => {
            this.http.call(payload, url, "", true).subscribe((data:any)=>{
                resolve(data);
            });
        });
    }

    loadMissions(idTier : number,
                 offset : number,
                 mode : string,
                 constraints : SearchQuery) {
        let factory : MissionServiceFactory = new MissionServiceFactory(this.http, this.configuration);
        let strategy : MissionServiceStrategy = factory.create();
        return strategy.loadContracts(idTier, offset, mode, constraints);
    }

    loadMissionCalendar(idTier : number) {
        let factory : MissionServiceFactory = new MissionServiceFactory(this.http, this.configuration);
        let strategy : MissionServiceStrategy = factory.create();
        return strategy.loadMissionCalendar(idTier);
    }

    loadMissionDetails(idMission : number) {
        let factory : MissionServiceFactory = new MissionServiceFactory(this.http, this.configuration);
        let strategy : MissionServiceStrategy = factory.create();
        return strategy.loadMissionDetails(idMission);
    }

    loadContractLink(idMission : number) {
        let factory : MissionServiceFactory = new MissionServiceFactory(this.http, this.configuration);
        let strategy : MissionServiceStrategy = factory.create();
        return strategy.loadContractLink(idMission);
    }
}


