import {Component} from '@angular/core';
import {Storage} from "@ionic/storage";
import {Configuration} from '../../../config/voj-configuration';
import {FormType} from '../../../models/form-type.model';
import {VojPageHeaderType} from '../../../models/voj-page-header.model';
import {VojConfiguration} from '../../../models/voj-configuration.model';
import {ModalService} from '../../../services/modal.service';
import {CvServiceProvider} from "../../../providers/cv-services/cv-service/cv-service";
import {JobPracticeDTO} from "../../../dtos/job-practice.dto";

@Component({
    selector: 'jobs-list-page',
    templateUrl: 'jobs-list.html'
})
export class JobsListPage {
    config: VojConfiguration;
    headerData: VojPageHeaderType;
    formData: FormType;

    constructor(public configurationService: Configuration,
                public cvService : CvServiceProvider,
                public storage : Storage,
                public modalService: ModalService) {
        this.config = this.configurationService.configuration;

        this.headerData = <VojPageHeaderType>{
            title: 'MY_JOBS.MY_JOBS',
            style: 'any',
            actions: [],
            isCanceled: true
        };
        this.formData = <FormType>{
            overrideSubmit : true,
            fields: [
                {
                    type: 'autocompete-select',
                    name: 'job',
                    value: null,
                    required: true,
                    placeholder: 'MY_JOBS.JOB',
                    placeholder2: 'MY_JOBS.LEVEL',
                    visible: true,
                    label: 'MY_JOBS.LABEL',
                    id: 'job',
                    dataset: [

                    ],
                    dataset2: [
                        {id: 1, key: '1', value: 'Dbutant'},
                        {id: 2, key: '2', value: 'Expert'}
                    ],
                    selectTitle : 'MY_JOBS.LEVEL',
                    selectSubOptions :  {
                        title: 'Niveau',
                        subTitle: 'Niveau',
                        mode: 'md',
                        cancelText:'Annuler'
                    },
                    completeMethod: function(kws) {return this.completeJobs(kws);}.bind(this)
                },
            ],
            submit: null
        };

        this.storage.get('currentUser').then((sdata : string)=>{

            let data = JSON.parse(sdata);
            this.cvService.loadPracticeJobs(data.id).then((data : Array<JobPracticeDTO>)=>{
                this.formData.fields[0].selectedValues = [];
                this.formData.fields[0].selectedIds = [];
                for(let i = 0 ; i < data.length ; i++) {
                    this.formData.fields[0].selectedValues.push(data[i].job.label+' - '+data[i].level);
                    this.formData.fields[0].selectedIds.push(data[i].job.id+'-'+data[i].levelId);
               }
            });
        });

    }


    updateJobs() {
        this.storage.get('currentUser').then((sdata : string)=>{
            let data = JSON.parse(sdata);
            let idjobyer = data.id;
            let jobs : Array<JobPracticeDTO> = [];
            if(this.formData.fields[0].selectedIds) {
                for(let i = 0 ; i <this.formData.fields[0].selectedIds.length ; i++) {
                    let j : JobPracticeDTO = new JobPracticeDTO();
                    j.job.id = parseInt(this.formData.fields[0].selectedIds[i].split('-')[0]);
                    j.levelId = parseInt(this.formData.fields[0].selectedIds[i].split('-')[1]);
                    jobs.push(j);
                }
            }

            this.cvService.savePracticeJobs(idjobyer, jobs);
        });
    }

    completeJobs(keyWords : string) {
        return this.cvService.completeJobs(keyWords);
    }

    ionViewDidLoad() {
        console.log('ionViewDidLoad BankSettingsPage');
    }

}



