import {Component} from '@angular/core';
import {NavController, NavParams} from "ionic-angular";
import {VojCardModel} from "../../models/voj-card.model";
import {EventBusProvider} from "../../providers/voj-events/voj-events";
import {NavigationService} from "../../services/navigation.service";
import {VojPageHeaderType} from "../../models/voj-page-header.model";
import {SearchServiceProvider} from "../../providers/search-service/search-service";
import {OfferInterestServiceProvider} from "../../providers/offer-service/offer-interest-service/offer-interest-service";
import {Storage} from "@ionic/storage";
import {ButtonType} from "../../models/button-type.model";
import {Configuration} from "../../config/voj-configuration";

@Component({
    selector: 'page-details',
    templateUrl: 'details.html',
})
export class DetailsPage {

    card: VojCardModel;
    headerData: VojPageHeaderType;
    searchMode: boolean;

    constructor(public navParams: NavParams,
                private searchService : SearchServiceProvider,
                private eventBus : EventBusProvider,
                private storage : Storage,
                private configuration : Configuration,
                private interestService : OfferInterestServiceProvider,
                private navigationService: NavigationService) {
        this.headerData = new VojPageHeaderType();
        this.headerData.title = "Dtails";
        this.card = new VojCardModel();
        this.card.id = 0;
        if(navParams.data && navParams.data.card) {
            this.card = navParams.data.card;
            if(this.searchService.eventMode) {
                if(this.card.badges && this.card.badges.length>0)
                    for(let i = 0 ; i < this.card.badges.length ; i++) {
                        if(this.card.badges[i].id=='read-mark'
                            && this.card.badges[i].icon != 'mail-open') {
                            this.searchService.markAsRead(this.card);
                            this.eventBus.publishAppEventUpdate('read');
                            break;
                        }
                    }

            }
            this.updateCardInterest();

            if(navParams.data.mode && navParams.data.mode=='search' && this.configuration.configuration.customer == 'E') {
                this.storage.get('currentUser').then((sdata:string)=>{
                    if(sdata && sdata.length>0) {
                        let data = JSON.parse(sdata);
                        let idEmployer = data.id;
                        this.searchService.loadFavData(this.card.id, idEmployer).then((fav : any)=>{
                            if(fav == true) {
                                this.loadUnfavButton();
                            } else {
                                this.loadFavButton();
                            }
                        });
                    }
                });

            }
        }
        this.eventBus.subscribeDetails(this);
    }

    loadUnfavButton() {
        let b : ButtonType = new ButtonType();
        b.icon = 'heart';
        b.id = 'fav';
        b.action = () => {
            this.storage.get('currentUser').then((sdata:string)=>{
                if(sdata && sdata.length>0) {
                    let data = JSON.parse(sdata);
                    let idEmployer = data.id;
                    this.searchService.switchFav(this.card.id, idEmployer);
                    this.card.socialButtons[0].icon = this.card.socialButtons[0].icon=='heart-outline'?'heart':'heart-outline';
                }
            });

        };
        this.card.socialButtons=[];
        this.card.socialButtons.push(b);
    }

    loadFavButton() {
        let b : ButtonType = new ButtonType();
        b.icon = 'heart-outline';
        b.id = 'fav';
        b.action = () => {
            this.storage.get('currentUser').then((sdata:string)=>{
                if(sdata && sdata.length>0) {
                    let data = JSON.parse(sdata);
                    let idEmployer = data.id;
                    this.searchService.switchFav(this.card.id, idEmployer);
                    this.card.socialButtons[0].icon = this.card.socialButtons[0].icon=='heart-outline'?'heart':'heart-outline';
                }
            });

        };
        this.card.socialButtons=[];
        this.card.socialButtons.push(b);
    }

    ionViewDidLoad() {

    }


    updateCard(c: VojCardModel) {
        this.card = c;
        if(this.card.badges && this.card.badges.length>0)
            for(let i = 0 ; i < this.card.badges.length ; i++) {
                if(this.card.badges[i].id=='read-mark'
                    && this.card.badges[i].icon != 'mail-open') {
                    this.searchService.markAsRead(this.card);
                    this.eventBus.publishAppEventUpdate('read');
                    break;
                }
            }
        this.updateCardInterest();
    }

    updateCardInterest(){
        if(!this.card.actions || this.card.actions.length == 0)
            return;
        for(let i = 0 ; i < this.card.actions.length ; i++) {
            let action = this.card.actions[i];
            if(action.id=='recruite') {
                action.action = () => {
                    this.switchInterest(this.card);
                }
            }
        }
    }

    switchInterest(c : VojCardModel) {
        this.storage.get("currentUser").then((sdata: any) => {
            //  connected
            let data = JSON.parse(sdata);
            let idJobyer = data.id;
            this.interestService.switchInterest(idJobyer, c.id).then((resp:any)=>{
                c.actions[0].label = resp.id>0?"Cette offre ne m'intresse plus":"Cette offre m'intresse";
                this.eventBus.updateCardInterest(c.id, resp.id>0);
            });

        });

    }

    noData() {
        return !this.card || this.card.id == 0;
    }

    hideDetailsView() {
        this.navigationService.hide('details');
    }
}



