import {VojCardModel} from "../models/voj-card.model";
import {TextType} from "../models/text-type";
import {DateUtils} from "../utils/utils";
import {SegmentType} from "../models/segment-type";
import {ButtonType} from "../models/button-type.model";
export class OfferDTO {
    id : number = 0;
    title : string = '';
    hourlyWage : number = 0.0;
    startDate : string = '';
    endDate : string = '';
    state : string = '';
    adress : string = '';
    days : Array<String>;
    hours : string = '';
    cost : number = 0.0;
    cover : string;
    slots : Array<OfferSlotDTO> = [];

    extractCard() : VojCardModel {
        this.calculateCost();
        this.calculateDays();
        let c : VojCardModel = new VojCardModel();
        c.id = this.id;
        c.title = <TextType> {
            icon : '',
            label : this.title
        };
        c.subtitles = [<TextType>{
            id : 'adress',
            icon : 'pin',
            label : this.adress,
            rendered : true
        }];
        c.cover = this.cover;
        if(this.days && this.days.length>0
            && this.state != 'ABSTRACT_OFFER')
            c.subtitles.push(<TextType>{
                id : 'calendar',
                icon : 'calendar',
                label : 'Mission de '+this.days.length+' jour(s)  partir du '+this.days[0],
                rendered : true
            });


        let hw : number = parseFloat(this.hourlyWage+'');
        c.subtitles.push(<TextType>{
            id : 'hour_wage',
            icon : 'cash',
            label : 'Taux horaire (/heure) : '+hw.toLocaleString("fr-FR", {minimumFractionDigits: 2, maximumFractionDigits: 2}),
            rendered : false
        });

        if(this.cost>0 && this.state != 'ABSTRACT_OFFER') {
            c.subtitles.push(<TextType>{
                id : 'work_time',
                icon : 'clock',
                label : "Nombre d'heures de travail : "+this.hours,
                rendered : false
            });
            c.subtitles.push(<TextType>{
                id : 'cost',
                icon : 'cash',
                label : 'Cot total estim () : '+this.cost.toLocaleString("fr-FR", {minimumFractionDigits: 2, maximumFractionDigits: 2}),
                rendered : false
            });
        } else {
            this.cost = parseFloat(this.hourlyWage+'');
        }
        c.segments = [<SegmentType>{
            title : {
                icon : '',
                label : ''+this.days.length
            },
            subtitle : {
                icon : '',
                label : 'Jour(s)'
            },
            action : () => {
                for(let i = 0 ; i < c.subtitles.length ; i++) {
                    let s = c.subtitles[i];
                    if(s.id == 'adress' || s.id == 'calendar')
                        s.rendered = true;
                    else
                        s.rendered = false;
                }
                c.segments[0].stateful = true;
                c.segments[1].stateful = false;
            },
            style:'',
            stateful : true
        },<SegmentType>{
            title : {
                icon : '',
                label : ''+this.cost.toLocaleString("fr-FR", {minimumFractionDigits: 2, maximumFractionDigits: 2})
            },
            subtitle : {
                icon : '',
                label : 'Cot estim ()'
            },
            action : () => {
                for(let i = 0 ; i < c.subtitles.length ; i++) {
                    let s = c.subtitles[i];
                    if(s.id == 'adress' || s.id == 'calendar')
                        s.rendered = false;
                    else
                        s.rendered = true;
                }
                c.segments[0].stateful = false;
                c.segments[1].stateful = true;
            },
            style:'',
            stateful : false
        }];

        c.actions = [];
        c.actions.push(<ButtonType>{
            id : 'details',
            icon : '',
            label : 'Dtails',
            action : ()=>{},
            style : {'background-color' : '#D339F8', 'color' : '#ffffff'}
        });

        return c;
    }

    calculateCost(){
        if(this.state == 'ABSTRACT_OFFER'){
            this.cost = parseFloat(''+this.hourlyWage);
            this.hours = '00:00';
            return;
        }

        let minutes = 0.0;
        for(let i = 0 ; i < this.slots.length ; i++){
            let sh : number = parseInt(this.slots[i].startHour+'');
            let eh : number = parseInt(this.slots[i].endHour+'');
            if(sh<eh){
                minutes+=eh-sh;
            } else {
                minutes+=1440-eh+sh;
            }
        }
        this.cost = minutes*this.hourlyWage/60;

        this.hours = DateUtils.intToTime(minutes);
    }

    calculateDays() {
        let days : Array<string> = [];
        if(this.state == 'ABSTRACT_OFFER'){
            this.days = [];
            return;
        }

        for(let i = 0 ; i < this.slots.length ; i++) {
            let date = DateUtils.sqlToDisplay(this.slots[i].startDate.split(' ')[0]);
            let found : boolean = false;
            for(let j = 0 ; j < days.length ; j++) {
                if(days[j] == date) {
                    found = true;
                    break;
                }
            }
            if(!found)
                days.push(date);
            date = DateUtils.sqlToDisplay(this.slots[i].endDate.split(' ')[0]);
            found = false;
            for(let j = 0 ; j < days.length ; j++) {
                if(days[j] == date) {
                    found = true;
                    break;
                }
            }
            if(!found)
                days.push(date);
        }
        this.days = days;
    }
}

export class OfferSlotDTO {
    id : number = 0;
    startDate : string = '';
    endDate : string = '';
    startHour : number = 0;
    endHour : number = 0;
}



