import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, Platform } from 'ionic-angular';
import {VojConfiguration} from "../../../models/voj-configuration.model";
import {VojPageHeaderType} from "../../../models/voj-page-header.model";
import {Configuration} from "../../../config/voj-configuration";
import {NavigationService} from "../../../services/navigation.service";
import {VojCardModel} from "../../../models/voj-card.model";
import {ButtonType} from "../../../models/button-type.model";



@IonicPage()
@Component({
  selector: 'page-offer-detail',
  templateUrl: 'offer-detail.html',
})
export class OfferDetailPage {
  card : VojCardModel;
  config: VojConfiguration;
  headerData: VojPageHeaderType;
  isMobile: boolean = false;
  informationResumeData: Array<{}>;
  actions : Array<ButtonType>;

  constructor(public navCtrl: NavController,
              public configurationService: Configuration,
              public platform: Platform,
              public navParams: NavParams) {
    this.card = this.navParams.data.card;
    let title : string = this.card.title.label.length<35?
        this.card.title.label:
        this.card.title.label.substring(0,35)+' ...';

    this.headerData = <VojPageHeaderType>{
      title: title,
      style: 'any',
      actions: [{
        id: 'help',
        label: 'help',
        icon: 'help',
        action: function () {
          console.log('button works')
        }
      }],
      isCanceled: true
    };
    this.informationResumeData = [
      {
        icon: 'md-create',
        label: 'Job',
        action: () => {}
      },
      {
        icon: 'md-create',
        label: 'Horaires de travail',
        action: () => {}
      },
      {
        icon: 'md-create',
        label: 'Coordonnes de la mission',
        action: () => {}
      },
      {
        icon: 'md-create',
        label: "Conditions d'emploi",
        action: () => {}
      },
      {
        icon: 'md-create',
        label: 'Prrequis',
        action: () => {}
      },
      {
        icon: 'md-create',
        label: 'Jobyer idal',
        action: () => {}
      }
    ];

    this.actions = [<ButtonType>{
      id : 'plan',
      label : 'Planifier',
      style : {'background-color' : '#3f51b5', 'color' : '#ffffff'},
      action : ()=>{console.log('Planif, planif coooooool')}
    }];

  }

  ionViewDidLoad() {

  }

}



