import {Injectable} from "@angular/core";
import {ToastController} from 'ionic-angular';
import {VojConfiguration} from "../models/voj-configuration.model";
import {Configuration} from "../config/voj-configuration";
import { TranslateService } from '@ngx-translate/core';

@Injectable()
export class ToastService {
    config: VojConfiguration;

    constructor(public configurationService: Configuration,
                public toastCtrl: ToastController,
                private translate: TranslateService) {
        this.config = this.configurationService.configuration;
    }

    presentEditingToast() {
        this.translate.get('TOASTS').subscribe(
            translation => {
                let toast = this.toastCtrl.create({
                    message: translation.EDITINGMESSAGE,
                    duration: 5000,
                    position: this.config.isLargeScreen ? 'top' : 'mobile',
                    cssClass: this.config.isLargeScreen ? 'toast-top-desktop' : 'toast-bottom-mobile',
                    showCloseButton: true,
                    closeButtonText: 'x'
                });
                toast.present();
            })
    }

    presentEnteredToast() {
        this.translate.get('TOASTS').subscribe(
            translation => {
                let toast = this.toastCtrl.create({
                    message: translation.ENTERINGMESSAGE,
                    duration: 5000,
                    position: this.config.isLargeScreen ? 'top' : 'mobile',
                    cssClass: this.config.isLargeScreen ? 'toast-top-desktop' : 'toast-bottom-mobile',
                    showCloseButton: true,
                    closeButtonText: 'x'
                });
                toast.present();
            })
    }

	presentErrorToast() {
        this.translate.get('TOASTS').subscribe(
            translation => {
                let toast = this.toastCtrl.create({
                    message: translation.ERRORMESSAGE,
                    duration: 5000,
                    position: this.config.isLargeScreen ? 'top' : 'mobile',
                    cssClass: this.config.isLargeScreen ? 'toast-top-desktop' : 'toast-bottom-mobile',
                    showCloseButton: true,
                    closeButtonText: 'x'
                });
                toast.present();
            })
	}

	presentWelcomeToast(){
		let toast = this.toastCtrl.create({
			message: 'Bienvenue sur Vit-On-Job, votre compte a bien t cr',
			duration: 5000,
			position: 'top'
		});
		toast.present();
	}
}


