import {Component} from '@angular/core';
import {IonicPage, NavParams, Platform, ViewController} from 'ionic-angular';
import {VojPageHeaderType} from '../../../models/voj-page-header.model';
import {MasterdataService} from "../../../providers/masterdata-service/masterdata-service";
import {PersonalDetailsIdentityPage} from '../personal-details-identity/personal-details-identity';
import {ProfileService} from '../../../providers/profile-service/profile-service';
import {DatasetType} from '../../../models/dataset-type';
import {CountryDto} from "../../../dtos/country.dto";
import {DepartmentDto} from '../../../dtos/department.dto';
import {MunicipalityDto} from '../../../dtos/municipality.dto';
import {VojConfiguration} from "../../../models/voj-configuration.model";
import {VojFormModel} from "../../../models/voj-form.model";
import {Configuration} from "../../../config/voj-configuration";
import {NavigationService} from "../../../services/navigation.service";
import {CurrentUserDto} from "../../../dtos/current-user.dto";
import {Storage} from "@ionic/storage";
import {StatusResponseDto} from "../../../dtos/status-response.dto";
import {VojPage} from "../../../models/voj-page";
import {ObjectUtils, StringUtils} from "../../../utils/utils";
import {ToastService} from "../../../services/toast.service";
import {NumSSConstraints} from "./numss-constraints";
import {FieldValidity} from "../../../models/field-validity";
import {BirthDataDTO} from "../../../dtos/birth-data.dto";
import { TranslateService } from '@ngx-translate/core';

@IonicPage()
@Component({
	selector: 'personal-details-page',
	templateUrl: 'personal-details.html',
})
export class PersonalDetailsPage{
	headerData: VojPageHeaderType;
	formData: VojFormModel;

	config: VojConfiguration;
	currentUser: CurrentUserDto;
	isInscription: boolean;

	foreignDepartmentList: Array<DatasetType>;
	franceDepartmentList: Array<DatasetType>;

	municipalities: Array<MunicipalityDto>;
	departments: Array<DepartmentDto>;
	countries: Array<CountryDto>;

    isErrorShown: boolean = false;
	
	constructor(private configuration: Configuration,
				public platform: Platform,
				private profileService: ProfileService,
				private masterdataService: MasterdataService,
				private navParams: NavParams,
				public viewCtrl: ViewController,
				private navigationService: NavigationService,
				private storage: Storage,
				public toast: ToastService,
                private translate: TranslateService) {
		this.config = this.configuration.configuration;
		this.isInscription = this.navParams.data.isInscription;

		this.headerData = {
			title: 'PERSONAL_DETAILS.TITLE',
			isCanceled: true,
			actions: [{
				id: 'help',
				label: 'help',
				icon: 'help',
				action: function () {
					console.log('button works')
				}
			}],

		};

		this.formData = {
			fields: [
				{
					required: true,
					type: 'date',
					label: '',
					value: null,
					placeholder: 'PERSONAL_DETAILS.DATE_OF_BIRTH',
					visible: true,
					disabled: false,
					id: 'date-of-birth',
					max: new Date(),
					//validationMsg: 'PERSONAL_DETAILS.DATE_OF_BIRTH_ERR',
					name: 'dateOfBirth'
				},
				{
					required: true,
					type: 'search-select',
					value: '',
					label: 'PERSONAL_DETAILS.COUNTRY_OF_BIRTH',
					dataset: [],
					visible: true,
					validationMsg: 'PERSONAL_DETAILS.COUNTRY_OF_BIRTH_ERR',
					id: 'country-of-birth',
					name: 'countryBirth',
                    selectTitle: 'SEARCH_SELECT.COUNTRIES_LIST',
					action: () => {
						this.countryChanged();
					}
				},
				{
					required: true,
					type: 'search-select',
					value: '',
					label: 'PERSONAL_DETAILS.DEPARTMENT_OF_BIRTH',
					dataset: [],
					visible: true,
					validationMsg: 'PERSONAL_DETAILS.DEPARTMENT_OF_BIRTH_ERR',
					id: 'department-of-birth',
					name: 'departmentOfBirth',
                    selectTitle: 'SEARCH_SELECT.DEPARTMENTS_LIST',
					action: () => {
						this.departmentChanged();
					}
				},
				{
					required: true,
					type: 'search-select',
					value: '',
					label: 'PERSONAL_DETAILS.PLACE_OF_BIRTH',
					dataset: [],
					visible: true,
					validationMsg: 'PERSONAL_DETAILS.PLACE_OF_BIRTH',
					id: 'place-of-birth',
					name: 'placeOfBirth',
                    selectTitle: 'SEARCH_SELECT.MUNICIPALITIES_LIST'
				},
				{
					required: true,
					type: 'text',
					value: '',
					label: 'PERSONAL_DETAILS.SOCIAL_SECURITY_NUMBER',
					visible: true,
					validationMsg: 'PERSONAL_DETAILS.SOCIAL_SECURITY_NUMBER',
					id: 'social-security-number',
					name: 'socialSecurityNumber',
                    action: () => {
                        // Hide validate message
                        this.hideValidateMessage();
                    }
				}
			],
			submit: {
				label: (this.isInscription ? 'PERSONAL_DETAILS.FOLLOWING_BTN' : 'PERSONAL_DETAILS.VALIDATE_BTN'),
				id: 'valid-btn',
				icon: 'no',
				action: () => {
					this.savePersonalDetails();
				}
			}
		};
	}

	ngOnInit(){
		//TODO: field should not be accessed by index
		this.formData.fields[0].max = this.setMaxBirthDay();

		// initializing departments for french and foreign people
		this.franceDepartmentList = [];
		this.foreignDepartmentList = [
			{
				value: '99',
				key: 'Etranger',
				id: 99
			}
		];

		//loading countries list
		this.masterdataService.loadCountriesList().then((data: CountryDto[]) => {
			//set dataset of country of birth of the form
			this.countries = data;
			this.formData.fields.filter(f => f.name === "countryBirth")[0].dataset = this.masterdataService.convertCountriesListToDataset(data);
		});

		//initializing form with current user data
		this.storage.get("currentUser").then((val) => {
			if (ObjectUtils.isEmpty(val)) {
				return;
			}
			this.currentUser = JSON.parse(val);
			this.initializationPersonalDetails();
		});
	}

	// Set data for current user in form component
	initializationPersonalDetails() {
		//In case of success, getPersonalDetails function returns a BirthDataDTO object
		this.profileService.getPersonalDetails(this.currentUser.id).then((data: any) => {
			if(data.status && data.status != "success"){
				this.toast.presentErrorToast();
				console.log(data.error);
			}else{
				let savedBirthData: BirthDataDTO = data;
				this.formData.fields.filter(f => f.name == 'dateOfBirth')[0].value = new Date(savedBirthData.birthDate);
				this.formData.fields.filter(f => f.name == 'countryBirth')[0].value = StringUtils.preventNull(savedBirthData.birthPlace.country.name);
				this.formData.fields.filter(f => f.name == 'departmentOfBirth')[0].value = StringUtils.preventNull(savedBirthData.birthPlace.department.name);
				this.formData.fields.filter(f => f.name == 'placeOfBirth')[0].value = StringUtils.preventNull(savedBirthData.birthPlace.municipality.name);
				this.formData.fields.filter(f => f.name == 'socialSecurityNumber')[0].value = StringUtils.preventNull(savedBirthData.numSS);

				let placeOfBirth = this.formData.fields.filter(f => f.name == 'placeOfBirth')[0].value;
				if (!ObjectUtils.isEmpty(placeOfBirth)) {
                    this.initializationDepartments();
				}
			}
		});
	}

	savePersonalDetails() {
		//TODO: call this peace of code when we will add the "Ignore this step" link
		//if all fields are empty, ignore this step and go to the next page
		/*let ignoreThisStep = !ObjectUtils.isFormNotEmpty(this.formData.fields);
		if (ignoreThisStep) {
			this.gotoPersonalDetailsIdentity();
			return;
		}*/

		//get entered and selected values
		let numSS = this.formData.fields.filter(f => f.name === "socialSecurityNumber")[0].value;
		let countryName = this.formData.fields.filter(f => f.name === "countryBirth")[0].value;
		let departmentName = this.formData.fields.filter(f => f.name === "departmentOfBirth")[0].value;
		let municipalityName = this.formData.fields.filter(f => f.name === "placeOfBirth")[0].value;
		let isFrench = (countryName == 'France');

		//if the user is french, save his department and municipality, else don't save them
		let birthData = {
			birthDate: StringUtils.preventNull(this.formData.fields.filter(f => f.name === "dateOfBirth")[0].value),
			country: this.countries.filter(c => c.name == countryName)[0].gid,
			department: (isFrench ? this.departments.filter(c => c.name == departmentName)[0].gid : ""),
			municipality: (isFrench ? this.municipalities.filter(c => c.name == municipalityName)[0].id : ""),
		};

		//the validity of numSS should be checked only for french people
		if (countryName == 'France') {
			if(!this.isNumSSValid(numSS, birthData)){
				return;
			}
		}

		//if everything is alright, save personal details
		this.profileService.savePersonalDetails(this.currentUser.id, birthData, numSS).then((data: StatusResponseDto) => {
			if (data.status == "success") {
				if (this.isInscription) {
					this.toast.presentEnteredToast();
					this.gotoPersonalDetailsIdentity();
				} else {
					this.toast.presentEditingToast();
				}
			} else {
				this.toast.presentErrorToast();
				console.log(data.error);
			}
		});
	}

	/*
	Checking if the numSS is valid. if it is not the case a validation msg should be displayed below the field
	 */
	isNumSSValid(numSS: string, birthData: any){
		let civility: string = this.currentUser.identity.civility;
		let selectedMunicipality: MunicipalityDto = this.municipalities.filter(m => m.id == birthData.municipality)[0];
		let numSSValidity: FieldValidity = NumSSConstraints.checkNumss(numSS, civility, birthData.birthDate, selectedMunicipality);
		if(!numSSValidity.isValid){
			this.showValidateMessage(numSSValidity.hint, 'social-security-number');
			// this.formData.fields.filter(f => f.name = 'placeOfBirth')[0].validationMsg = numSSValidity.hint;
			return false
		}else{
			return true;
		}
	}

	gotoPersonalDetailsIdentity() {
		this.navigationService.dismiss(this.viewCtrl);
		let vojPage: VojPage = {
			page: PersonalDetailsIdentityPage,
			isWebModal: true,
			params: {isInscription: this.isInscription}
		};
		this.navigationService.navigate("main", vojPage);
	}

	/*
	When a country is chosen, the associated department list is loaded
	 */
	countryChanged() {
		let chosenCountry = this.formData.fields.filter(f => f.name === "countryBirth")[0].value

		if(!ObjectUtils.isEmpty(chosenCountry)) {
			// If France
			if (chosenCountry === 'France') {
				//if the birth country is France, load department list, and disable municipalities list
				//municipalities list should be activated pnly after chosing a department
				this.masterdataService.loadDepartmentList().then((data: DepartmentDto[]) => {
					this.departments = data;
					this.franceDepartmentList = this.masterdataService.convertDepartmentsListToDataset(data);

                    this.formData.fields.filter(f => f.name === "departmentOfBirth")[0].dataset = this.franceDepartmentList;
                    this.formData.fields.filter(f => f.name === "placeOfBirth")[0].dataset = [];

                    this.formData.fields.filter(f => f.name === "departmentOfBirth")[0].readonly = false;
                    this.formData.fields.filter(f => f.name === "departmentOfBirth")[0].required = true;
                    this.formData.fields.filter(f => f.name === "placeOfBirth")[0].required = true;

                    this.formData.fields.filter(f => f.name === "departmentOfBirth")[0].value === ' ' ? this.formData.fields.filter(f => f.name === "departmentOfBirth")[0].value = '' : null;
                    this.formData.fields.filter(f => f.name === "placeOfBirth")[0].value === ' ' ? this.formData.fields.filter(f => f.name === "placeOfBirth")[0].value = '' : null;


				});
			}
			// If not France
			else if (chosenCountry !== 'France') {
				//department and municipality cannot be filled if birth country is not France

                this.formData.fields.filter(f => f.name === "departmentOfBirth")[0].readonly = true;
                this.formData.fields.filter(f => f.name === "placeOfBirth")[0].readonly = true;

                this.formData.fields.filter(f => f.name === "departmentOfBirth")[0].dataset = this.foreignDepartmentList;
                this.formData.fields.filter(f => f.name === "placeOfBirth")[0].dataset = [];

                this.formData.fields.filter(f => f.name === "departmentOfBirth")[0].value = ' ';
                this.formData.fields.filter(f => f.name === "placeOfBirth")[0].value = ' ';

                this.formData.fields.filter(f => f.name === "departmentOfBirth")[0].required = false;
                this.formData.fields.filter(f => f.name === "placeOfBirth")[0].required = false;
			}
		}
	}

	/*
	When a department is chosen, the associated municipalities list is loaded
	 */
	departmentChanged() {
		// Get Municipalities for Department
		let departmentName = this.formData.fields.filter(f => f.name == "departmentOfBirth")[0].value;
		if(!ObjectUtils.isEmpty(departmentName) && !ObjectUtils.isEmpty(this.departments) && departmentName !== ' ') {
			let departmentId = this.departments.filter(d => d.name == departmentName)[0].gid;
			this.profileService.getMunicipalitiesByDepartment(departmentId).then((data: MunicipalityDto[]) => {
				this.municipalities = data;
                this.formData.fields.filter(f => f.name === "placeOfBirth")[0].readonly = false;
                this.formData.fields.filter(f => f.name === "placeOfBirth")[0].value = '';
                this.formData.fields.filter(f => f.name === "placeOfBirth")[0].dataset = this.profileService.convertMunicipalitiesToDataset(data);
			});
		}
	}

    initializationDepartments () {
        this.masterdataService.loadDepartmentList().then((data: DepartmentDto[]) => {
            this.departments = data;
			let departmentName = this.formData.fields.filter(f => f.name == "departmentOfBirth")[0].value;
            let departmentId = this.departments.filter(d => d.name == departmentName)[0].gid;

            this.profileService.getMunicipalitiesByDepartment(departmentId).then((data: MunicipalityDto[]) => {
                this.municipalities = data;
                this.formData.fields.filter(f => f.name === "placeOfBirth")[0].dataset = this.profileService.convertMunicipalitiesToDataset(data);
            });
        });
	}

	ionViewDidLoad() {
		this.showWarningBirthDayMessage();
	}

	showWarningBirthDayMessage() {
        this.translate.get('PERSONAL_DETAILS').subscribe(
            translation => {

                let warningDiv = document.createElement('div');
                warningDiv.setAttribute('id', 'voj-warning-msg');
                warningDiv.innerHTML = `<p>${translation.WARNING_BIRTH_MESSAGE}</p>`;
                warningDiv.className = 'voj-warning-msg';

                let parentId = 'date-of-birth';
                document.getElementById(parentId).appendChild(warningDiv);
            }
        )
	}

    showValidateMessage(warningMsg: string, parentId: string) {
        if (this.isErrorShown === true) {
            return;
        }
        this.isErrorShown = true;

        let warningDiv = document.createElement('div');
        warningDiv.setAttribute('id', 'voj-error-msg');
        warningDiv.innerHTML = `<p>${warningMsg}</p>`;
        warningDiv.className = 'voj-validation-error-msg';

        document.getElementById(parentId).appendChild(warningDiv);
    }

    hideValidateMessage() {
        // Hide validate message if it exists
        if (this.isErrorShown === false) {
            return;
        }
        if(document.getElementById('voj-error-msg')) {
            document.getElementById('voj-error-msg').remove();
        }
        this.isErrorShown = false;
    }

	setMaxBirthDay() {
		let [date, month, year] = [String(new Date().getDate()), String(new Date().getMonth()),
			String(new Date().getFullYear() - 18)];

		Number(date) < 10 ? date = '0' + date : date;
		Number(month) < 10 ? month = '0' + month : month;

		return new Date(`${year}-${month}-${date}`);
	}
}



