import {FieldType} from "../models/field-type";

export class StringUtils{
	public static preventNull(str: string): string{
		if(ObjectUtils.isEmpty(str)){
			return "";
		}else{
			return str;
		}
	}


}

export class ArrayUtils{
}

export class ObjectUtils{
	public static isEmpty(obj: any): boolean{
		if(obj == "" || obj == null || !obj || obj.length == 0 || obj == 'null'){
			return true;
		}else{
			return false;
		}
	}

	/**
	 * Checking if at least one field is filled
	 */
	public static isFormNotEmpty(fields: Array<FieldType>): boolean {
		return fields.some(this.isFieldNotEmpty);
	}

	private static isFieldNotEmpty(element: FieldType, index: number, array: Array<FieldType>): boolean {
		return !ObjectUtils.isEmpty(element.value);
	}
}

export class DateUtils {
	public static sqlToDisplay(sdate : string) : string {
		if(!sdate || sdate.split('-').length<3)
			return sdate;

		let date = sdate.split(' ')[0].split('-');

		return date[2]+'/'+date[1]+'/'+date[0];
	}

	public static xmlToDisplay(sdate : string) : string {
	if(!sdate || sdate.split('-').length<3)
		return sdate;

	let date = sdate.split('T')[0].split('-');

	return date[2]+'/'+date[1]+'/'+date[0];
}

	public static sqlToXML(sdate : string){
		if(!sdate)
			return "";
		return sdate.replace(' ','T').replace('.0','Z');
	}

	public static xmlToSql(sdate : string){
		if(!sdate)
			return "";
		return sdate.replace('T',' ').replace('Z','.0');
	}

	public static intToTime(intTime : number){
		let h : number = Math.floor(intTime/60);
		let m : number = intTime%60;
		let fulltime = (h<10?'0':'')+h+':'+(m<10?'0':'')+m;
		return fulltime;
	}
}

export class FieldValidate {
    public static isValid(str: string): boolean {
        let regCity = /^[A-Za-z-.' \-\p{L}\p{Zs}\p{Lu}\p{Ll}']+$/;
        return regCity.test(str);
    }
}


