import {Component} from '@angular/core';

import {VojPageHeaderType} from '../../../models/voj-page-header.model';
import {VojFormModel} from '../../../models/voj-form.model';
import {VojConfiguration} from "../../../models/voj-configuration.model";
import {Configuration} from "../../../config/voj-configuration";
import {Events, NavParams, ViewController} from "ionic-angular";
import {CurrentUserDto} from "../../../dtos/current-user.dto";
import {Storage} from "@ionic/storage";
import {MainAddressPage} from "../main-address/main-address";
import {IdentityDto} from "../../../dtos/identity.dto";
import {StatusResponseDto} from "../../../dtos/status-response.dto";
import {NavigationService} from "../../../services/navigation.service";
import {VojPage} from "../../../models/voj-page";
import {ObjectUtils} from "../../../utils/utils";
import {ToastService} from "../../../services/toast.service";
import {ProfileService} from "../../../providers/profile-service/profile-service";
import {AccountDto} from "../../../dtos/account.dto";
import {SignupService} from "../../../providers/signup-service/signup-service";


@Component({
	selector: 'identity-page',
	templateUrl: 'identity.html',
})
export class IdentityPage{

	dataPageHeader: VojPageHeaderType;
	formData: VojFormModel;

	config: VojConfiguration;
	currentUser: CurrentUserDto;
	isInscription: boolean;		//flag to know if we are in the inscription process

	identityDTO: IdentityDto;

	constructor(public configurationService: Configuration,
				public profileService: ProfileService,
				public events: Events,
				public viewCtrl: ViewController,
				private storage: Storage,
				private navigationService: NavigationService,
				private navParams: NavParams,
				public toast: ToastService,
				public signupService: SignupService) {
		this.config = this.configurationService.configuration;
		this.isInscription = this.navParams.data.isInscription;
		this.identityDTO = new IdentityDto();

		// Set page header
		this.dataPageHeader = {
			title: 'Ma civilit',
			actions: [
				{
					id: 'help',
					icon: 'help',
					action: () => {
						console.log('Click help button.')
					}
				}
			],
			isCanceled: true
		};

		// Set form data
		this.formData = {
			fields: [
				// Radio for civility
				{
					type: 'radio',
					value: "Mme",
					label: 'Civilit',
					dataset: [
						{id: 1, key: 'Mme', value: 'Mme'},
						{id: 2, key: 'Mr', value: 'Mr'}
					],
					required: true,
					id: 'civility',
					name: 'civility',
					action: () => {
						this.checkCivility();
					}
				},
				// Text for name
				{
					type: 'text',
					value: "",
					label: 'Nom',
					required: true,
					visible: true,
					//regex: '^[A-Za-z-\' ]*$',
					regex: '^[A-Za-z-.\' \\-\\p{L}\\p{Zs}\\p{Lu}\\p{Ll}\']+$',
					id: 'lastname',
					name: 'lastName',
					validationMsg: "Veuillez saisir un nom valide"
				},
				// Text for middle name
				{
					type: 'text',
					value: "",
					label: 'Nom de jeune fille',
					required: false,
					visible: true,
					//regex: '^[A-Za-z-\' ]*$',
					regex: '^[A-Za-z-.\' \\-\\p{L}\\p{Zs}\\p{Lu}\\p{Ll}\']+$',
					id: 'middlename',
					name: 'middleName',
					validationMsg: "Veuillez saisir un nom de jeune fille valide"
				},
				// Text for Prnom
				{
					type: 'text',
					value: "",
					label: 'Prnom',
					required: true,
					visible: true,
					//regex: '^[A-Za-z-\' ]*$',
					regex: '^[A-Za-z-.\' \\-\\p{L}\\p{Zs}\\p{Lu}\\p{Ll}\']+$',
					id: 'firstname',
					name: 'firstName',
					validationMsg: "Veuillez saisir un prnom valide"
				},
			],
			submit: {
				id: "submit",
				label: (this.isInscription ? 'S\'inscrire' : 'Valider'),
				action: (event) => {
					this.saveIdentity();
				}
			}
		};
	}

	ngOnInit() {
		this.storage.get("currentUser").then((data: string) => {
			if (ObjectUtils.isEmpty(data)) {
				//TODO: restrict access to this page to logged in users
				//redirecting to the search page was a bad idea, we should redirect to a landing page or display a notification
				return;
			}
			this.currentUser = JSON.parse(data);

			this.initializingFormData()
		});
	}

	initializingFormData(){
		// Set form data in the case of connected users
		this.formData.fields.filter(f => f.name === "civility")[0].value = (ObjectUtils.isEmpty(this.currentUser.identity.civility) ? "Mme" : this.currentUser.identity.civility);
		this.formData.fields.filter(f => f.name === "lastName")[0].value = this.currentUser.identity.lastName;
		this.formData.fields.filter(f => f.name === "middleName")[0].value = this.currentUser.identity.middleName;
		this.formData.fields.filter(f => f.name === "firstName")[0].value = this.currentUser.identity.firstName;
	}

	/*
	updating the visibility of middlename field according to the civility value
	 */
	checkCivility() {
		if (this.formData.fields.filter(f => f.name === "civility")[0].value === 'Mme') {
			this.formData.fields.filter(f => f.name === "middleName")[0].visible = true;
		}
		else {
			this.formData.fields.filter(f => f.name === "middleName")[0].visible = false;
		}
	}

	saveIdentity() {
		//if all fields are empty, ignore this step and go to the next page
		/*let ignoreThisStep = !ObjectUtils.isFormNotEmpty(this.formData.fields);
		if(ignoreThisStep && this.isInscription){
			this.goToMainAddressPage();
			return;
		}*/

		//prepare identity data to save
		let firstname = this.formData.fields.filter(f => f.name === "firstName")[0].value;
		let civility = this.formData.fields.filter(f => f.name === "civility")[0].value;

		let identityDto: IdentityDto = {
			civility: civility,
			lastName: this.formData.fields.filter(f => f.name === "lastName")[0].value.toUpperCase(),
			middleName: (civility != "Mme" ? "" : this.formData.fields.filter(f => f.name === "middleName")[0].value.toUpperCase()),
			//first letter in uppercase, like 'Amal'
			firstName: firstname.charAt(0).toUpperCase() + firstname.slice(1)
		};

		if(this.isInscription){
			let accountToSave: AccountDto = this.navParams.data.accountToSave;
			let password: string = this.navParams.data.password;

			this.signupService.signup(accountToSave, password, identityDto).then((data: StatusResponseDto) => {
				if (data.status == "success") {
					//if the user is sign up successfully : store new user in localDB
					this.currentUser = {
						id: data.data,
						account: accountToSave,
						identity: identityDto
					};
					this.storage.set("currentUser", JSON.stringify(this.currentUser)).then(() => {
						//in the case of inscription: we should dismiss the identity modal/page and show the MainAddress modal/page
						this.toast.presentEnteredToast();
						this.goToMainAddressPage();
					});
					// publishing the identity data updates
					this.publishIdentityData();
				} else {
					this.toast.presentErrorToast();
					console.log(data.error);
				}
			});
		}else{
			this.profileService.saveIdentity(identityDto, this.currentUser.id).then((data: StatusResponseDto) => {
				if (data.status == "success") {
					this.currentUser.identity = identityDto;
					this.storage.set("currentUser", JSON.stringify(this.currentUser)).then(() => {
						//TODO: ask JAKJOUD, alert the user to verify his bank settings
						this.toast.presentEditingToast();
					});
					// publishing the identity data updates
					this.publishIdentityData();
				} else {
					this.toast.presentErrorToast();
					console.log(data.error);
				}
			});
		}
	}

	// publishing the identity data updates
	publishIdentityData(){
		this.events.publish('user:update', this.currentUser);
	}

	goToMainAddressPage() {
		this.navigationService.dismiss(this.viewCtrl);
		let vojPage: VojPage = {page: MainAddressPage, isWebModal: true, params: {isInscription: this.isInscription}};
		this.navigationService.navigate("main", vojPage);
	}
}


