import {Component} from '@angular/core';
import {VojPageHeaderType} from "../../models/voj-page-header.model";
import {VojFormModel} from "../../models/voj-form.model";
import {NavigationService} from "../../services/navigation.service";
import {VojPage} from "../../models/voj-page";
import {OptionsPage} from "../account-module/options/options";
import {md5} from "../../scripts/md5";
import {Storage} from "@ionic/storage";
import {HttpRequestProvider} from "../../providers/http-request/http-request";

@Component({
	selector: 'new-password-page',
	templateUrl: 'new-password.html',
})
export class NewPasswordPage {
	dataPageHeader: VojPageHeaderType;
	formData: VojFormModel;
    currentUser: any;
	constructor(public navigationService: NavigationService,
                private storage: Storage,
                private http: HttpRequestProvider) {
		// Set page header
		this.dataPageHeader = {
			title: 'NEW_PASSWORD.TITLE',
			actions: [
				{
					id: 'help',
					icon: 'help',
					action: () => {
						console.log('Click help button.')
					}
				}
			],
			isCanceled: true
		};

		// Set form data
		this.formData = {
			fields: [
                {
                    type: 'password',
                    value: '',
                    label: 'NEW_PASSWORD.OLDPASSWDORD',
                    required: true,
                    readonly: false,
                    visible: true,
                    min: 6,
                    id: '',
                    name: 'oldPassword',
                    validationMsg: 'VALIDATION.PASSWORD_VALIDATION_MSG',
                },
                {
                    type: 'password',
                    value: '',
                    label: 'NEW_PASSWORD.NEWPASSWDORD',
                    required: true,
                    readonly: false,
                    visible: true,
                    min: 6,
                    id: '',
                    name: 'userPassword',
                    validationMsg: 'VALIDATION.PASSWORD_VALIDATION_MSG',
                },
                {
                    type: 'password',
                    value: '',
                    label: 'NEW_PASSWORD.CONFIRMPASSWDORD',
					required: true,
                    readonly: false,
                    visible: true,
                    min: 6,
                    id: '',
                    name: 'passwordConfirmation',
                    validationMsg: 'VALIDATION.PASSWORD_VALIDATION_MSG',
                },
			],
			submit: {
				id: "submit",
                label: 'NEW_PASSWORD.VALIDATE',
				action: () => {
                  this.checkPassword();
				}
			}
		};
	}

    checkPassword() {
        let oldPassword = md5(this.formData.fields.filter(f => f.name === "oldPassword")[0].value);
        let newPassword = md5(this.formData.fields.filter(f => f.name === "userPassword")[0].value);
        let passwordConfirmation = md5(this.formData.fields.filter(f => f.name === "passwordConfirmation")[0].value);
        let url = 'https://hermes.vitonjob.com/profile/';
        if (newPassword !== passwordConfirmation) {
            return false;
        }
        let userData = {
            "service": "CHANGE_PASSWORD",
            "user_id": this.currentUser.account.id,
            "old_password": oldPassword,
            "new_password": newPassword
        };
        return new Promise(resolve => {
            this.http.call(userData, url, "", true).subscribe((data: any) => {
                resolve(data);
                return data.status === 'failure' ? false : alert('Your password has been changed'), this.goOptions();
            });
        });

    }

	goOptions() {
        let vojNav: VojPage = { page: OptionsPage, isRoot: true };
        this.navigationService.navigate('list', vojNav);
	}

    ngOnInit() {
        this.storage.get("currentUser").then((data: string) => {
            this.currentUser = JSON.parse(data);
        });
    }

}


