import { Injectable } from '@angular/core';
import 'rxjs/add/operator/map';
import {HttpRequestProvider} from "../../http-request/http-request";
import {Configuration} from "../../../config/voj-configuration";
import {VojTuple} from "../../../models/voj-tuple.model";
import {OfferDTO} from "../../../dtos/offer.dto";
import {VojCardModel} from "../../../models/voj-card.model";

@Injectable()
export class OfferServiceProvider {

  myOffers:Array<OfferDTO>=[];

  constructor(public http: HttpRequestProvider,
              public configuration : Configuration) {

  }

  loadMyOffers(idEnterprise : number) {
    let payload = {
      service : 'MY_OFFERS',
      employer : idEnterprise
    };
    let url = "";
    try {
      for (let i = 0; i < this.configuration.configuration.urls.length; i++) {
        let t : VojTuple = this.configuration.configuration.urls[i];
        if (t.key == "offers") {
          url = t.value;
          break;
        }
      }
    }catch(exception){
      console.log("loading occured before initlaizing configuration");
    }

    return new Promise(resolve => {
      this.http.call(payload, url, "", true).subscribe((data : any)=>{
        console.clear();
        console.log(JSON.stringify(data));
        this.myOffers = [];
        let cards : Array<VojCardModel> = [];
        for(let i = 0 ; i < data.length ; i++) {
          let o : OfferDTO = new OfferDTO();
          o.id = data[i].id;
          o.title = data[i].title;
          o.hourlyWage = data[i].hourWage;
          o.startDate = data[i].startDate;
          o.endDate = data[i].endDate;
          o.state = data[i].state;
          o.adress = data[i].adress;
          o.slots = data[i].slots;
          o.cover = data[i].cover;

          cards.push(o.extractCard());
          this.myOffers.push(o);
        }
        resolve(cards);
      });
    });
  }

}



