import {Component, OnInit} from '@angular/core';
import {Storage} from '@ionic/storage';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import {VojCardModel} from "../../../models/voj-card.model";
import {OfferInterestServiceProvider} from "../../../providers/offer-service/offer-interest-service/offer-interest-service";
import {SearchServiceProvider} from "../../../providers/search-service/search-service";
import {VojPageHeaderType} from "../../../models/voj-page-header.model";



@IonicPage()
@Component({
  selector: 'page-my-interests',
  templateUrl: 'my-interests.html',
})
export class MyInterestsPage implements OnInit {
    pageHeader: VojPageHeaderType;
    cards : Array<VojCardModel> = [];

    constructor(public navCtrl: NavController,
                public searchService : SearchServiceProvider,
                public interestService : OfferInterestServiceProvider,
                private storage: Storage,
                public navParams: NavParams) {

    }

    ngOnInit() {
        this.pageHeader = new VojPageHeaderType();
        this.pageHeader.title = "Les offres qui m'intressent";
        this.searchService.getCards(this.interestService.offerInterests).then((data:any)=>{
            this.cards = data;
            for(let i = 0 ; i < this.cards.length ; i++) {
                this.cards[i].actions = this.cards[i].actions.splice(0,1);
                let action = this.cards[i].actions[0];
                action.action = ()=>{
                    this.switchInterest(this.cards[i]);
                };
            }
        });

    }

    switchInterest(c : VojCardModel) {
        this.storage.get("currentUser").then((sdata: any) => {
            let data = JSON.parse(sdata);
            let idJobyer = data.id;
            this.interestService.switchInterest(idJobyer, c.id).then((resp:any)=>{
                let index = -1;
                for(let i = 0 ; i < this.cards.length ; i++){
                    if(this.cards[i].id == c.id){
                        index = i;
                        break;
                    }
                }
                if(index>=0)
                    this.cards.splice(index, 1);
            });
        });

    }

    ionViewDidLoad() {

    }

}



