import { Injectable } from '@angular/core';
import 'rxjs/add/operator/map';
import {HttpRequestProvider} from "../../http-request/http-request";
import {Configuration} from "../../../config/voj-configuration";
import {VojTuple} from "../../../models/voj-tuple.model";
import {JobDTO} from "../../../dtos/job.dto";
import {JobPracticeDTO} from "../../../dtos/job-practice.dto";
import {AvailabilityDTO} from "../../../dtos/availabilities.dto";
import {VojCalendarModel} from "../../../models/voj-calendar.model";


@Injectable()
export class CvServiceProvider {

  constructor(private http: HttpRequestProvider,
              private configuration : Configuration) {

  }

    completeJobs(keywords : string){
        let payload = {
            service : "AUTOCOMPLETE_JOB",
            keywords : keywords
        };
        let url = null;
        for (let i = 0; i < this.configuration.configuration.urls.length; i++) {
            let t : VojTuple = this.configuration.configuration.urls[i];
            if (t.key == "cv") {
                url = t.value;
                break;
            }
        }

        return new Promise(resolve => {
            this.http.backgroundCall(payload, url).subscribe((data:any)=>{
                let jobs = [];
                for(let i = 0 ; i < data.length ; i++){
                    let j ={id : data[i].idJob, value : data[i].label};
                    jobs.push(j);
                }
                resolve(jobs);
            });
        });
    }

    loadPracticeJobs(idJobyer : number) {
        let payload = {
            service : "GET_PROFILE_JOB",
            idjobyer : idJobyer
        };
        let url = null;
        for (let i = 0; i < this.configuration.configuration.urls.length; i++) {
            let t : VojTuple = this.configuration.configuration.urls[i];
            if (t.key == "cv") {
                url = t.value;
                break;
            }
        }

        return new Promise(resolve => {
            this.http.call(payload, url, "", true).subscribe((data:any)=>{

                let jobs = [];
                for(let i = 0 ; i < data.jobs.length ; i++){
                    let job = data.jobs[i];
                    let j : JobPracticeDTO = new JobPracticeDTO();
                    j.job = new JobDTO();
                    j.job.id = job.id;
                    j.job.label = job.label;
                    j.levelId = job.idlevel;
                    j.level = j.levelId == 1?'Dbutant':'Expert';
                    jobs.push(j)
                }
                resolve(jobs);
            });
        });
    }

    savePracticeJobs(idJobyer : number, jobs : Array<JobPracticeDTO>) {
        let payload = {
            service : 'SET_PROFILE_JOB',
            idjobyer : idJobyer,
            jobs : jobs
        };

        let url = null;
        for (let i = 0; i < this.configuration.configuration.urls.length; i++) {
            let t : VojTuple = this.configuration.configuration.urls[i];
            if (t.key == "cv") {
                url = t.value;
                break;
            }
        }

        return new Promise(resolve => {
            this.http.call(payload, url, "", true).subscribe((data:any)=>{
                resolve(data);
            });
        });
    }

    loadAvailabilities(idJobyer) {
        let payload = {
            service : 'GET_AVAILABILITY',
            idjobyer : idJobyer
        };

        let url = null;
        for (let i = 0; i < this.configuration.configuration.urls.length; i++) {
            let t : VojTuple = this.configuration.configuration.urls[i];
            if (t.key == "cv") {
                url = t.value;
                break;
            }
        }

        return new Promise(resolve => {
            this.http.call(payload, url, "", true).subscribe((data:any)=>{
                let availabilities : Array<AvailabilityDTO> = [];
                for(let i = 0 ; i < data.length ; i++) {
                    let a : AvailabilityDTO = new AvailabilityDTO();
                    a.id = parseInt(data[i].id);
                    a.expression = data[i].expression;
                    availabilities.push(a);
                }

                resolve(availabilities);
            });
        });
    }

    deleteAvailability(idAvailability, idJobyer) {
        let payload = {
            service : 'DEL_AVAILABILITY',
            idavailability : idAvailability,
            idjobyer : idJobyer
        };

        let url = null;
        for (let i = 0; i < this.configuration.configuration.urls.length; i++) {
            let t : VojTuple = this.configuration.configuration.urls[i];
            if (t.key == "cv") {
                url = t.value;
                break;
            }
        }

        return new Promise(resolve => {
            this.http.call(payload, url, "", true).subscribe((data:any)=>{
                resolve(data);
            });
        });
    }

    saveFullAvailability(idJobyer, daysOfWeek) {
        let payload = {
            service : 'FULL_AVAILABILITY',
            idjobyer: idJobyer,
            days : daysOfWeek
        };

        let url = null;
        for (let i = 0; i < this.configuration.configuration.urls.length; i++) {
            let t : VojTuple = this.configuration.configuration.urls[i];
            if (t.key == "cv") {
                url = t.value;
                break;
            }
        }

        return new Promise(resolve => {
            this.http.call(payload, url, "", true).subscribe((data:any)=>{
                resolve(data);
            });
        });
    }
}



