import {Configuration} from "../../config/voj-configuration";
import {VojConfiguration} from "../../models/voj-configuration.model";
import {VojTuple} from "../../models/voj-tuple.model";
import {HttpRequestProvider} from "../http-request/http-request";
import {CountryDto} from "../../dtos/country.dto";
import {Injectable} from "@angular/core";
import {NationalityDto} from "../../dtos/nationality.dto";
import {DatasetType} from '../../models/dataset-type';
import {DepartmentDto} from '../../dtos/department.dto';
import {PrefectureDto} from '../../dtos/prefecture.dto';

@Injectable()
export class MasterdataService{
	config: VojConfiguration;
	url: string;

	constructor(private configuration: Configuration,
				private http: HttpRequestProvider) {
		this.config = this.configuration.configuration;
		this.url = this.config.urls.filter(u => u.key == "masterdata")[0].value;
	}

	loadCountriesList() {
		let payload = {
			"type": "COUNTRIES",
		};
		return new Promise(resolve => {
			this.http.call(payload, this.url, "", true).subscribe((data: CountryDto[]) => {
				resolve(data);
			});
		});
	}

	loadNationalitiesList() {
		let payload = {
			"type": "NATIONALITIES",
		};
		return new Promise(resolve => {
			this.http.call(payload, this.url, "", true).subscribe((data: NationalityDto[]) => {
				resolve(data);
			});
		});
	}

	loadDepartmentList() {
		let payload = {
			"type": "DEPARTMENTS",
		};
		return new Promise(resolve => {
			this.http.call(payload, this.url, "", true).subscribe((data: DepartmentDto[]) => {
				resolve(data);
			});
		});
	}

	loadPrefectureList() {
		let payload = {
			"type": "PREFECTURES"
		};
		return new Promise(resolve => {
			this.http.call(payload, this.url, "", true).subscribe((data: PrefectureDto[]) => {
				resolve(data);
			});
		});
	}

	convertCountriesListToDataset(countries: CountryDto[], forPhoneCodes?: boolean) {
		let dataset: Array<DatasetType> = [];
		for (let i = 0; i < countries.length; i++) {
			let ds = {
				id: countries[i].gid,
				key: countries[i].name + (forPhoneCodes ? " (" + countries[i].phoneCode + ")" : ""),
				value: countries[i].phoneCode
			};
			dataset.push(ds);
		}
		return dataset;
	}

	convertNationalitiesListToDataset(nationalities: NationalityDto[]) {
		let dataset: Array<DatasetType> = [];
		for (let i = 0; i < nationalities.length; i++) {
			let ds = {id: nationalities[i].gid, key: nationalities[i].name, value: nationalities[i].gid.toString()};
			dataset.push(ds);
		}
		return dataset;
	}

	convertDepartmentsListToDataset(departments: DepartmentDto[]) {
		let dataset: Array<DatasetType> = [];
		for (let i = 0; i < departments.length; i++) {
			let ds = {id: departments[i].gid, key: departments[i].name, value: departments[i].gid.toString()};
			dataset.push(ds);
		}
		return dataset;
	}

	convertPrefectureListToDataset(prefectures: PrefectureDto[]) {
		let dataset: Array<DatasetType> = [];
		for (let i = 0; i < prefectures.length; i++) {
			let ds = {id: prefectures[i].gid, key: prefectures[i].name, value: prefectures[i].gid.toString()};
			dataset.push(ds);
		}
		return dataset;
	}
}


