import {VojCalendarCompiler} from "./voj-calendar-compiler";
import {VojCalendarSlots, VojCalendarModel} from "../../../models/voj-calendar.model";
import {DateUtils} from "../../../utils/utils";

export class FullSlotCompiler extends VojCalendarCompiler {
    parse(expression : string) : VojCalendarModel {
        let c : VojCalendarModel = new VojCalendarModel();
        c.type = 'FULL';
        return c;
    }

    encode(model : VojCalendarModel) : string {
        let m = model.clone();
        return JSON.stringify(m);
    }

    display(model : VojCalendarModel) : Array<string> {
        let slots : Array<string> = [];
        slots.push("Toujours disponible");
        return slots;
    }
}

export class SimpleSlotCompiler extends VojCalendarCompiler {
    parse(expression : string) : VojCalendarModel {
        let model :VojCalendarModel = JSON.parse(expression);
        let slotsExp = model.slots;
        let slots : Array<VojCalendarSlots> = [];

        for(let i = 0 ; i < slotsExp.length ; i++) {
            let s : VojCalendarSlots = new VojCalendarSlots();
            s.startDay = DateUtils.sqlToXML(slotsExp[i].startDay);
            s.endDay = DateUtils.sqlToXML(slotsExp[i].endDay);
            s.startHour = slotsExp[i].startHour;
            s.endHour = slotsExp[i].endHour;
            slots.push(s);
        }
        model.slots = slots;
        return model;
    }

    encode(model : VojCalendarModel) : string {
        let m = model.clone();

        for(let i = 0 ; i < m.slots.length ; i++) {
            m.slots[i].startDay = DateUtils.xmlToSql(m.slots[i].startDay);
            m.slots[i].endDay = DateUtils.xmlToSql(m.slots[i].endDay);
        }
        return JSON.stringify(m);
    }

    display(model : VojCalendarModel) : Array<string> {
        let displaySlots : Array<string> = [];
        for(let i = 0 ; i < model.slots.length ; i++) {
            let startD : string = DateUtils.xmlToDisplay(model.slots[i].startDay);
            let endD : string = DateUtils.xmlToDisplay(model.slots[i].endDay);
            if(startD == endD)
                displaySlots.push(
                    "Le "+startD+
                    " de "+DateUtils.intToTime(model.slots[i].startHour)+
                    "  "+DateUtils.intToTime(model.slots[i].endHour)
                );
            else
                displaySlots.push(
                    "Du "+startD+
                    " au "+endD+
                    " de "+DateUtils.intToTime(model.slots[i].startHour)+
                    "  "+DateUtils.intToTime(model.slots[i].endHour)
                );
        }
        return displaySlots;
    }
}

export class WeekSlotCompiler extends VojCalendarCompiler {
    parse(expression : string) : VojCalendarModel {
        let model = JSON.parse(expression);
        let slotsExp = model.slots;
        let slots : Array<VojCalendarSlots> = [];

        //  expecting to have one instance for the start hour and the end hour
        for(let i = 0 ; i < slotsExp.length ; i++) {
            let s : VojCalendarSlots = new VojCalendarSlots();
            s.startDay = DateUtils.sqlToXML(slotsExp[i].startDay);
            s.endDay = DateUtils.sqlToXML(slotsExp[i].endDay);
            s.startHour = slotsExp[i].startHour;
            s.endHour = slotsExp[i].endHour;
            slots.push(s);
        }
        model.slots = slots;
        return model;
    }

    encode(model : VojCalendarModel) : string {
        let m = model.clone();

        //  leave this method empty for further implementation

        return JSON.stringify(m);
    }

    display(model : VojCalendarModel) : Array<string> {
        let displaySlots = [];

        let days = model.daysOfWeek;
        if(!days || days.length ==0)
            return [];

        if(days[0] == '*') {
            let ds = "Disponible tous les jours de la semaine ";
            if(model.slots[0] && model.slots[0].startDay)
                ds = ds+"du "+DateUtils.xmlToDisplay(model.slots[0].startDay)+" "
            if(model.slots[0] && model.slots[0].endDay)
                ds = ds+"au "+DateUtils.xmlToDisplay(model.slots[0].endDay)+" "
            if(model.slots[0] && model.slots[0].startHour)
                ds = ds+"de "+DateUtils.intToTime(model.slots[0].startHour)+" "
            if(model.slots[0] && model.slots[0].endHour)
                ds = ds+" "+DateUtils.intToTime(model.slots[0].endHour)+" "
            displaySlots.push(ds);
        } else {
            let ds = "Disponible tous les ";
            for(let i = 0 ; i < days.length ; i++){
                ds = ds + this.displayDay(days[i])+", "
            }
            ds = ds.substring(0, ds.length-2);
            if(model.slots[0] && model.slots[0].startDay)
                ds = ds+"du "+DateUtils.xmlToDisplay(model.slots[0].startDay)+" "
            if(model.slots[0] && model.slots[0].endDay)
                ds = ds+"au "+DateUtils.xmlToDisplay(model.slots[0].endDay)+" "
            if(model.slots[0] && model.slots[0].startHour)
                ds = ds+"de "+DateUtils.intToTime(model.slots[0].startHour)+" "
            if(model.slots[0] && model.slots[0].endHour)
                ds = ds+" "+DateUtils.intToTime(model.slots[0].endHour)+" "
            displaySlots.push(ds);
        }


        return displaySlots;
    }

    displayDay(day : string){
        let d : number = parseInt(day);
        switch(d) {
            case 0: return 'Lundi';
            case 1: return 'Mardi';
            case 2: return 'Mercredi';
            case 3: return 'Jeudi';
            case 4: return 'Vendredi';
            case 5: return 'Samedi';
            default: return 'Dimanche';
        }
    }
}

export class MonthSlotCompiler extends VojCalendarCompiler {
    parse(expression : string) : VojCalendarModel {
        let model = JSON.parse(expression);
        let slotsExp = model.slots;
        let slots : Array<VojCalendarSlots> = [];

        //  expecting to have one instance for the start day/hour and the end day/hour
        for(let i = 0 ; i < slotsExp.length ; i++) {
            let s : VojCalendarSlots = new VojCalendarSlots();
            s.startDay = DateUtils.sqlToXML(slotsExp[i].startDay);
            s.endDay = DateUtils.sqlToXML(slotsExp[i].endDay);
            s.startHour = slotsExp[i].startHour;
            s.endHour = slotsExp[i].endHour;
            slots.push(s);
        }
        model.slots = slots;
        return model;
    }

    encode(model : VojCalendarModel) : string {
        let m = model.clone();

        //  leave this method empty for further implementation

        return JSON.stringify(m);
    }

    display(model : VojCalendarModel) : Array<string> {
        let displaySlots = [];

        let days = model.months;
        if(!days || days.length ==0)
            return [];

        if(days[0] == '*') {
            let ds = "Disponible tous les jours de l'anne ";
            if(model.slots[0].startHour)
                ds = ds+"de "+DateUtils.intToTime(model.slots[0].startHour)+" "
            if(model.slots[0].endHour)
                ds = ds+" "+DateUtils.intToTime(model.slots[0].endHour)+" "
            displaySlots.push(ds);
        } else {
            let ds = "Disponible le ";
            for(let i = 0 ; i < days.length ; i++){
                ds = ds + this.displayMonth(days[i])+", "
            }
            ds = ds.substring(0, ds.length-2);
            if(model.slots[0].startDay)
                ds = ds+"du "+DateUtils.xmlToDisplay(model.slots[0].startDay)+" "
            if(model.slots[0].endDay)
                ds = ds+"au "+DateUtils.xmlToDisplay(model.slots[0].endDay)+" "
            if(model.slots[0].startHour)
                ds = ds+"de "+DateUtils.intToTime(model.slots[0].startHour)+" "
            if(model.slots[0].endHour)
                ds = ds+" "+DateUtils.intToTime(model.slots[0].endHour)+" "
            displaySlots.push(ds);
        }


        return displaySlots;
    }

    displayMonth(month : string) {
        let d : number = parseInt(month);
        switch(d) {
            case 0: return 'Janvier';
            case 1: return 'Fvrier';
            case 2: return 'Mars';
            case 3: return 'Avril';
            case 4: return 'Mai';
            case 5: return 'Juin';
            case 6: return 'Juillet';
            case 7: return 'Aut';
            case 8: return 'Septembre';
            case 9: return 'Octobre';
            case 10: return 'Novembre';
            default: return 'Dcembre';
        }
    }
}


