import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { ReactiveFormsModule } from '@angular/forms';
import { TranslateModule } from '@ngx-translate/core';
import { IonicPageModule } from 'ionic-angular';
import {
	MdButtonModule,
	MdCheckboxModule,
	MdDatepickerModule,
	MdNativeDateModule,
	MdInputModule
} from '@angular/material';

export const MdImports = [
	MdButtonModule,
	MdCheckboxModule,
	MdDatepickerModule,
	MdNativeDateModule,
	MdInputModule
];

import { VojDynamicFormComponent } from './voj-dynamic-form';

@NgModule({
	declarations: [ VojDynamicFormComponent ],
	exports: [ VojDynamicFormComponent ],
	imports: [
		CommonModule,
		IonicPageModule.forChild(VojDynamicFormComponent),
		TranslateModule.forChild(),
		ReactiveFormsModule,
		MdImports
	]
})
export class VojDynamicFormModule {}



