import {HttpRequestProvider} from "../../http-request/http-request";
import {Configuration} from "../../../config/voj-configuration";
import {OfferInterestJobyer} from "./offer-interest-jobyer";

export class OfferInterestFactory {
    constructor(public http: HttpRequestProvider,
                public configuration : Configuration) {

    }

    create() {
        if(this.configuration.configuration.customer == 'J')
            return new OfferInterestJobyer(this.http, this.configuration);
        return new OfferInterestJobyer(this.http, this.configuration);
    }
}


