import {Component, OnInit} from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import {Storage} from '@ionic/storage';
import {OfferServiceProvider} from "../../../providers/offer-service/offer-interest-service/offer-service";
import {VojCardModel} from "../../../models/voj-card.model";
import {VojPageHeaderType} from "../../../models/voj-page-header.model";
import {Configuration} from "../../../config/voj-configuration";
import {NavigationService} from "../../../services/navigation.service";
import {VojPage} from "../../../models/voj-page";
import {OfferDetailPage} from "../offer-detail/offer-detail";

@IonicPage()
@Component({
  selector: 'page-my-offers-list',
  templateUrl: 'my-offers-list.html',
})
export class MyOffersListPage  implements OnInit {
  selectedCards : Array<VojCardModel> = [];
  cards : Array<VojCardModel> = [];
  publicCards : Array<VojCardModel> = [];
  privateCards : Array<VojCardModel> = [];
  archiveCards : Array<VojCardModel> = [];
  abstractCards : Array<VojCardModel> = [];
  pageHeader: VojPageHeaderType;

  offerType: string = 'PUBLIC';
  mobileStyle : any;

  constructor(public navCtrl: NavController,
              public offersService : OfferServiceProvider,
              public storage: Storage,
              public configuration : Configuration,
              public navigationService : NavigationService,
              public navParams: NavParams) {
  }

  ngOnInit() {
    this.pageHeader = new VojPageHeaderType();
    this.pageHeader.title = "Mes offres";
    this.storage.get('currentUser').then((sdata:string)=>{
      let data = JSON.parse(sdata);
      let idEnterprise = data.id;
      console.clear();
      console.log(sdata);
      this.offersService.loadMyOffers(idEnterprise).then((data:Array<VojCardModel>)=>{
        this.cards = data;
        for(let i = 0 ; i < this.cards.length ; i++) {
          let c = this.cards[i];
          c.actions[0].action = function(){
            this.showDetails(c);
          }.bind(this);
          let o = null;
          for(let j = 0 ; j < this.offersService.myOffers.length ; j++) {
            if(this.offersService.myOffers[j].id == c.id) {
              o = this.offersService.myOffers[j];
              break;
            }
          }

          if(o == null)
            continue;
          if(o.state == 'PUBLIC' || o.state == 'CONTRACT')
            this.publicCards.push(c);
          if(o.state == 'ARCHIVED')
            this.archiveCards.push(c);
          if(o.state == 'ABSTRACT_OFFER')
            this.abstractCards.push(c);
          if(o.state == 'PRIVATE')
            this.privateCards.push(c);
          this.selectedCards = this.publicCards;
        }
      });

    });

    if (!this.configuration.configuration.isLargeScreen) {
      this.mobileStyle = {
        "z-index": 1,
        "position": "relative",
        "background-color": "rgb(76, 84, 93)",
        "margin-top": "-15px"
      };

    } else {
      this.mobileStyle = {
        "margin-top": "-15px"
      };

    }
  }

  showDetails(c : VojCardModel){
    let vojNav: VojPage = <VojPage>{page: OfferDetailPage, params: {card: c}, isRoot: true};
    this.navigationService.navigate('details', vojNav);
  }

  switchOfferType(e) {
    if(this.offerType == 'PUBLIC')
      this.selectedCards = this.publicCards;
    if(this.offerType == 'ARCHIVED')
      this.selectedCards = this.archiveCards;
    if(this.offerType == 'ABSTRACT_OFFER')
      this.selectedCards = this.abstractCards;
    if(this.offerType == 'PRIVATE')
      this.selectedCards = this.privateCards;
  }

  ionViewDidLoad() {

  }

}



