import { Component } from '@angular/core';
import {IonicPage, NavController, NavParams, Platform} from 'ionic-angular';
import {CalendarType, CalendarTypeMode} from "../../../models/calendar.model";
import {VojPageHeaderType} from "../../../models/voj-page-header.model";
import {SlotTypeInterface} from "../../../models/slot-type.interface";
import {Storage} from "@ionic/storage";
import {DateUtils} from "../../../utils/utils";
import {FullCalendarService} from "../../../components/voj-calendar/full-calendar/full-calendar.service";
import {OfferServiceProvider} from "../../../providers/offer-service/offer-interest-service/offer-service";
import {OfferDTO} from "../../../dtos/offer.dto";
import {VojCardModel} from "../../../models/voj-card.model";

@IonicPage()
@Component({
  selector: 'page-my-offers-calendar',
  templateUrl: 'my-offers-calendar.html',
})
export class MyOffersCalendarPage {

  public calendarData: CalendarType;
  public calendarMode: CalendarTypeMode;
  public isDesktop: boolean;
  public slots : Array<SlotTypeInterface> = [];
  dataPageHeader: VojPageHeaderType;
  offers : Array<OfferDTO> = [];

  constructor(public platform: Platform,
              public offerService : OfferServiceProvider,
              public fullCalendarService: FullCalendarService,
              public storage : Storage) {
    this.isDesktop = this.platform.is('core');

    this.dataPageHeader = <VojPageHeaderType>{
      title: 'Calendrier de mes annonces',
      actions: [
        {
          id: 'help',
          label: '',
          icon: 'help',
          action: () => {
            console.log('Click help button.')
          }
        }
      ],
      style: 'any',
      isCanceled: true
    };

    this.calendarData = {
      dayClick: (date, jsEvent, view) => {
        console.log('[dayclick]', date.toString());
      },
      eventClick: (event, jsEvent, view) => {
        console.log('[eventclick]', event, view);
      },
      slots: []
    };


    this.storage.get("currentUser").then((sdata : any)=>{
      let ac = JSON.parse(sdata);
      this.offerService.loadMyOffers(ac.id).then((data:Array<VojCardModel>)=>{
        this.offers = this.offerService.myOffers;
        this.createSlots();
        this.calendarData = {
          dayClick: (date, jsEvent, view) => {
            console.log('[dayclick]', date.toString());
          },
          eventClick: (event, jsEvent, view) => {
            console.log('[eventclick]', event, view);
          },
          slots: this.slots
        };

        this.fullCalendarService.setSlots(this.slots);
      });
    });
  }

  createSlots() {
    for(let i = 0 ; i < this.offers.length ; i++) {
      let o = this.offers[i];
      let s: SlotTypeInterface;
      s = {
        id: parseInt(o.id+''),
        title: o.title,
        color: this.colorSlot(o.state),
        startDate: DateUtils.sqlToXML(o.startDate),
        endDate: DateUtils.sqlToXML(o.endDate),
        startHour: 3,
        endHour: 20,
        type: 'type 1',
        break: true,
        action: () => console.log(`Event ${96} triggered`)
      };
      this.slots.push(s);
    }
  }

  colorSlot(state) {
    if(state == 'PUBLIC' || state == 'CONTRACT')
      return '#eca613';
    else if (state == 'ARCHIVED')
      return '#D339F8';
    return '#427feb';

  }

  ionViewDidLoad() {
  }

}



