import {Injectable} from '@angular/core';
import 'rxjs/add/operator/map';
import {HttpRequestProvider} from "../http-request/http-request";
import {Configuration} from "../../config/voj-configuration";
import {VojConfiguration} from "../../models/voj-configuration.model";
import {AuthResponseDto} from "../../dtos/auth-response.dto";
import {AccountDto} from "../../dtos/account.dto";
import {IdentityDto} from "../../dtos/identity.dto";

@Injectable()
export class SignupService{

	config: VojConfiguration;
	url: string;

	constructor(public http: HttpRequestProvider,
				public configuration: Configuration) {
		this.config = this.configuration.configuration;
		this.url = this.config.urls.filter(u => u.key == "account")[0].value;
	}

	/*signup(phone: string, email: string, password: string) {
		let payload = {
			"service": "REGISTER",
			"role": this.config.customer == "J" ? "jobyer" : "employeur",
			"phone": phone,
			"email": email,
			"password": password
		};
		return new Promise(resolve => {
			this.http.call(payload, this.url, "", true).subscribe((data: any) => {
				resolve(data);
			});
		});
	}*/

	signup(account: AccountDto, password: string, identity: IdentityDto){
		let payload = {
			"service": "REGISTER",
			"role": this.config.customer == "J" ? "jobyer" : "employeur",
			"phone": account.phone,
			"email": account.mail,
			"password": password,
			"civility": identity.civility,
			"lastName": identity.lastName,
			"middleName": identity.middleName,
			"firstName": identity.firstName
		};
		return new Promise(resolve => {
			this.http.call(payload, this.url, "", true).subscribe((data: any) => {
				resolve(data);
			});
		});
	}

	checkExistingAccount(account: AccountDto) {
		let payload = {
			"service": "CHECK_USER",
			"role": this.config.customer == "J" ? "jobyer" : "employeur",
			"phone": account.phone,
			"email": account.mail
		};
		return new Promise(resolve => {
			this.http.call(payload, this.url, "", true).subscribe((data: AuthResponseDto) => {
				resolve(data);
			});
		});
	}
}



