import {Component, OnInit} from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { InAppBrowser } from '@ionic-native/in-app-browser';
import {BrowserModule} from '@angular/platform-browser';
import {Storage} from "@ionic/storage";
import {DomSanitizer} from "@angular/platform-browser";
import {Configuration} from "../../../config/voj-configuration";
import {MissionServiceProvider} from "../../../providers/mission-service/mission-service";
import {NavigationService} from "../../../services/navigation.service";
import {VojPage} from "../../../models/voj-page";
import {MissionsCalendarPage} from "../missions-calendar/missions-calendar";
import { AlertController } from 'ionic-angular';
import {PendingMissionsPage} from "../pending-missions/pending-missions";
import {DateUtils} from "../../../utils/utils";


@IonicPage()
@Component({
  selector: 'page-mission-signature',
  templateUrl: 'mission-signature.html',
})
export class MissionSignaturePage implements OnInit {

  private url : any;
  private literalUrl : string;
  webmode : boolean;
  interval = 10000;
  cid : number;
  timer : any;
  uid : number;
  constructor(public navCtrl: NavController,
              private iab: InAppBrowser,
              private configuration : Configuration,
              private domSanitizer : DomSanitizer,
              private navigationService : NavigationService,
              private missionService : MissionServiceProvider,
              private alertCtrl : AlertController,
              private storage : Storage,
              public navParams: NavParams) {
    this.literalUrl = this.navParams.data.link;
    this.cid = this.navParams.data.idContract;
    this.url = this.domSanitizer.bypassSecurityTrustResourceUrl(this.navParams.data.link);
    this.webmode = this.configuration.configuration.isLargeScreen;
    this.storage.get("currentUser").then((sdata)=>{
      let data = JSON.parse(sdata);
      this.uid = data.id;
    });
  }

  ngOnInit() {
    if(!this.webmode) {
      let browser = this.iab.create(this.literalUrl, '_self');
    }
    this.timer = setInterval(()=> {
      this.missionService.checkSignature(this.cid).then((data : any)=> {
        let status = data.status;
        if(status) {
          this.missionService.pendingMissions(this.uid);
          clearInterval(this.timer);
          let alert = this.alertCtrl.create({
            title: 'Flicitiations !',
            subTitle: "Votre mission va commencer le "+DateUtils.sqlToDisplay(data.start)+"  "+DateUtils.intToTime(data.startHour)+". Munissez vous, ce jour l, de vos documents d'identit et professionnels",
            buttons: ['OK']
          });
          alert.present();
          let p : VojPage = <VojPage>{page : MissionsCalendarPage, isRoot : true};
          this.navigationService.navigate('main',p);

        }

      });

    },this.interval);
  }

  ionViewDidLoad() {

  }

}



