import { Injectable } from '@angular/core';
import 'rxjs/add/operator/map';
import {HttpRequestProvider} from "../../http-request/http-request";
import {Configuration} from "../../../config/voj-configuration";
import {AvailabilitiesPage} from "../../../pages/cv-offer-module/availabilities/availabilities";

@Injectable()
export class AvailabilitiesDispatcherProvider {

    list : AvailabilitiesPage;

    constructor(private http: HttpRequestProvider,
              private configuration : Configuration) {
    }

    register(type : AVAILABILITY_PAGE, page : any) {
        switch(type){
            case AVAILABILITY_PAGE.LIST: this.list = <AvailabilitiesPage>page; break;
        }
    }
}

export enum AVAILABILITY_PAGE {
    LIST,
    ADD,
    ADD_WEEK,
    ADD_CUSTOM,
    CALENDAR
}


