import { Injectable } from '@angular/core';
import 'rxjs/add/operator/map';
import {OfferInterest} from "../../../dtos/offer-interest.dto";
import {Configuration} from "../../../config/voj-configuration";
import {Storage} from "@ionic/storage";
import {OfferInterestFactory} from "../offer-service-strategy/offer-interest-factory";
import {OfferInterestStrategy} from "../offer-service-strategy/offer-interest-algorithm";
import {HttpRequestProvider} from "../../http-request/http-request";
import {VojTuple} from "../../../models/voj-tuple.model";
import {SearchResultDTO} from "../../../dtos/search-result.dto";
import {SearchDTOFactory} from "../../../dtos/dto-strategy/search/search-dto-factory";


@Injectable()
export class OfferInterestServiceProvider {

    public offerInterests : Array<OfferInterest> = [];

    constructor(public http: HttpRequestProvider,
              public configuration : Configuration,
              private storage : Storage) {

    }



    getInterests() {
      let factory : OfferInterestFactory = new OfferInterestFactory(this.http, this.configuration)
      let offerInterestStrategy : OfferInterestStrategy = factory.create();
      this.storage.get("currentUser").then((sdata:string)=>{
         let data = JSON.parse(sdata);

         if(data && data.id) {
             offerInterestStrategy.getInterests(data.id).then((list : any)=> {
                this.offerInterests = [];
                 for(let j = 0 ; j < list.length ; j++) {
                     let i : OfferInterest = new OfferInterest();
                     i.id = list[j].id;
                     i.idJobyer = list[j].idJobyer;
                     i.idOffer = list[j].idOffer;
                     i.title = list[j].title;
                     this.offerInterests.push(i);
                 }
             });
         }
      });
    }

    switchInterest(idJobyer : number, idOffer : number) {
        debugger;
      let payload = {
          service : 'SWITCH_INTEREST',
          jobyer : idJobyer,
          offer : idOffer
      };
      console.log(JSON.stringify(payload));
      let url = null;
      for (let i = 0; i < this.configuration.configuration.urls.length; i++) {
          let t : VojTuple = this.configuration.configuration.urls[i];
          if (t.key == "interest") {
              url = t.value;
              break;
          }
      }

      return new Promise(resolve => {
          this.http.call(payload, url, "", true).subscribe((data:any)=>{
              if(data.id == 0){
                  let index = -1;
                  for(let i = 0 ; i < this.offerInterests.length ; i++) {
                      if(this.offerInterests[i].idOffer == idOffer){
                          index = i;
                          break;
                      }
                  }
                  if(this.offerInterests.length == 1)
                      this.offerInterests = [];
                  else if(index>=0)
                    this.offerInterests = this.offerInterests.splice(index,1);
              } else {
                  let i : OfferInterest = new OfferInterest();
                  i.id = data.id;
                  i.idJobyer = data.idJobyer;
                  i.idOffer = data.idOffer;
                  i.title = data.title;
                  this.offerInterests.push(i);
              }

              resolve(data);
          });
      });
    }

}



