import {Component} from '@angular/core';
import {IonicPage, NavParams, ViewController} from 'ionic-angular';
import {VojFormModel} from "../../../models/voj-form.model";
import {VojConfiguration} from "../../../models/voj-configuration.model";
import {VojPageHeaderType} from "../../../models/voj-page-header.model";
import {Configuration} from "../../../config/voj-configuration";
import {MasterdataService} from "../../../providers/masterdata-service/masterdata-service";
import {NationalityDto} from "../../../dtos/nationality.dto";
import {PrefectureDto} from '../../../dtos/prefecture.dto';
import {VojPage} from "../../../models/voj-page";
import {BankSettingsPage} from "../bank-settings/bank-settings";
import {NavigationService} from "../../../services/navigation.service";
import { DatasetType } from '../../../models/dataset-type';
import {ProfileService} from "../../../providers/profile-service/profile-service";
import {CurrentUserDto} from "../../../dtos/current-user.dto";
import {Storage} from "@ionic/storage";
import {StatusResponseDto} from "../../../dtos/status-response.dto";
import {ObjectUtils, StringUtils} from "../../../utils/utils";
import {ToastService} from "../../../services/toast.service";
import {IdentityCardDataDto} from "../../../dtos/identity-card-data.dto";
import {TranslateService} from '@ngx-translate/core';
import {DateUtils} from './date-utils';

/**
 * Generated class for the PersonalDetailsIdentityPage page.
 *
 * See http://ionicframework.com/docs/components/#navigation for more info
 * on Ionic pages and navigation.
 */

@IonicPage()
@Component({
	selector: 'page-personal-details-identity',
	templateUrl: 'personal-details-identity.html',
})
export class PersonalDetailsIdentityPage{
	config: VojConfiguration;
	isInscription: boolean;
	currentUser: CurrentUserDto;

	formData: VojFormModel;
	headerData: VojPageHeaderType;

	nationalitiesList: NationalityDto[];

	europeanDocsList: Array<DatasetType>;
    foreignDocsList: Array<DatasetType>;

    disabledChooseValid: boolean = false;

    constructor(public configurationService: Configuration,
				private masterdataService: MasterdataService,
				private navParams: NavParams,
				private navigationService: NavigationService,
				private viewCtrl: ViewController,
				private profileService: ProfileService,
				private storage: Storage,
                public toast: ToastService,
                private translate: TranslateService) {
		this.config = this.configurationService.configuration;
		this.isInscription = this.navParams.data.isInscription;

		this.formData = {
			fields: [
				{
					type: 'search-select',
					value: '',
					dataset: [],
					label: 'PERSONAL_DETAILS_IDENTITY.NATIONALITY',
					required: true,
					visible: true,
					id: 'nationality',
					name: 'nationality',
                    selectTitle: 'PERSONAL_DETAILS_IDENTITY.NATIONALITIES_LIST',
					action: () => {
						this.checkNationalities();
					}
				},
				{
					type: 'select',
					value: '',
					dataset: [],
					label: 'PERSONAL_DETAILS_IDENTITY.TYPE_CARD',
					required: true,
					visible: true,
					id: 'typeCard',
					name: 'typeCard',
					action: () => this.checkDocument()
				},
				{
					type: 'text',
					value: '',
					label: 'PERSONAL_DETAILS_IDENTITY.CARD_NUMBER',
					required: true,
					visible: true,
					regex: '^[A-Za-z0-9]*$',
					validationMsg: 'PERSONAL_DETAILS_IDENTITY.CARD_NUMBER_ERR',
					id: 'cardNumber',
					name: 'cardNumber',
				},
				{
					type: 'date',
					value: null,
					label: '',
					placeholder: 'PERSONAL_DETAILS_IDENTITY.DELIVERED_DATE',
					required: true,
					visible: true,
					id: 'deliveredDate',
                    disabled: false,
					max: new Date(),
					name: 'deliveredDate',
					action: () => {
						this.chooseDeliveredDate();
					}
				},
				{
					type: 'search-select',
					value: '',
					dataset: [],
					label: 'PERSONAL_DETAILS_IDENTITY.PREFECTURE',
					required: true,
					visible: true,
					id: 'prefecture',
					name: 'prefecture',
                    selectTitle: 'PERSONAL_DETAILS_IDENTITY.PREFECTURE_LIST'
				},
				{
					type: 'date',
					value: null,
                    label: '',
					placeholder: 'PERSONAL_DETAILS_IDENTITY.VALID_FROM',
					required: true,
					visible: true,
					id: 'validFrom',
					name: 'validFrom',
                    disabled: true,
                    readonly: true,
                    action: () => {
                        this.chooseValidFromDate();
                    }
				},
				{
					type: 'date',
					value: null,
					label: '',
                    placeholder: 'PERSONAL_DETAILS_IDENTITY.VALID_TO',
					required: true,
					visible: true,
					id: 'validTo',
					name: 'validTo',
                    disabled: true,
                    readonly: true
				}
			],
			submit: {
				id: 'submit',
				label: (this.isInscription ? 'PERSONAL_DETAILS_IDENTITY.FOLLOWING_BTN' : 'PERSONAL_DETAILS_IDENTITY.VALIDATE_BTN'),
				action: (event) => {
					this.savePersonalDetailsIdentity();
				}
			}
		};

		this.headerData = {
			title: 'PERSONAL_DETAILS_IDENTITY.TITLE',
			actions: [
				{
					id: 'help',
					icon: 'help',
					label: 'help',
					action: (event) => {
						console.log('help button works')
					}
				}
			],
			isCanceled: false
		};

		// Initialization of documents lists
        this.translate.get('DOCUMENT_TYPE_LIST').subscribe(
            translation => {
                this.europeanDocsList = [
                    {
                        id: 301,
                        key: translation.CNI,
                        value: '1'
                    },
                    {
                        id: 302,
                        key: translation.PASSPORT,
                        value: '2'
                    },
                    {
                        id: 303,
                        key: translation.MAM_EU_NATIONALS,
                        value: '3'
                    }
                ];

			this.foreignDocsList = [
				{
					id: 304,
					key: translation.MAP_OF_RESIDENTS,
					value: '4'
				},
				{
					id: 305,
					key: translation.RESIDENCE_PERMIT,
					value: '5'
				}
			];
            });
        this.setMaxDeliveryDate();
	}

    initializationPersonalDetails(jobyerId) {
		this.profileService.getPersonalIdentityDetails(jobyerId).then((data: any) => {
			if(data.status && data.status != "success"){
				this.toast.presentErrorToast();
				console.log(data.error);
			}else{
				let savedPersonalIdentityDetails: IdentityCardDataDto = data;
				this.formData.fields.filter(f => f.name == "nationality")[0].value = StringUtils.preventNull(savedPersonalIdentityDetails.nationality.name);
				this.formData.fields.filter(f => f.name == "typeCard")[0].value = +savedPersonalIdentityDetails.typeIDCard;
				this.formData.fields.filter(f => f.name == "cardNumber")[0].value = StringUtils.preventNull(savedPersonalIdentityDetails.numberIDCard);
				this.formData.fields.filter(f => f.name == "deliveredDate")[0].value = new Date(savedPersonalIdentityDetails.deliveredDate);
				this.formData.fields.filter(f => f.name == "prefecture")[0].value = StringUtils.preventNull(savedPersonalIdentityDetails.prefecture.name);
				this.formData.fields.filter(f => f.name == "validFrom")[0].value = new Date(savedPersonalIdentityDetails.validFrom);
				this.formData.fields.filter(f => f.name == "validTo")[0].value = new Date(savedPersonalIdentityDetails.validTo);
                ObjectUtils.isEmpty(savedPersonalIdentityDetails.validTo) === true ? this.disabledChooseValid = true : this.disabledChooseValid = false;
			}
		});
	}

    chooseDeliveredDate() {
        if (this.disabledChooseValid === false) {
				return;
        }
		// Change Delivered Date
        let deliveredDate = this.formData.fields.filter(f => f.name == "deliveredDate")[0].value;
        if (deliveredDate !== null) {
            this.formData.fields.filter(f => f.name == "validFrom")[0].disabled = false;
            this.formData.fields.filter(f => f.name == "validTo")[0].disabled = true;
            this.formData.fields.filter(f => f.name == "validFrom")[0].min = new Date(deliveredDate);
        }
		// Clear Delivered Date
		else if (deliveredDate === null) {
            this.formData.fields.filter(f => f.name == "validFrom")[0].disabled = true;
            this.formData.fields.filter(f => f.name == "validTo")[0].disabled = true;
		}
		// Clear next fields date
        // this.formData.fields[5].disabled = false;
        this.formData.fields.filter(f => f.name == "validFrom")[0].value = null;
        this.formData.fields.filter(f => f.name == "validTo")[0].value = null;
	}

    chooseValidFromDate() {
        // Change Valid From Date
        let validFrom = this.formData.fields.filter(f => f.name == "validFrom")[0].value;
        if (validFrom !== null) {
            this.formData.fields.filter(f => f.name == "validTo")[0].disabled = false;
            this.formData.fields.filter(f => f.name == "validTo")[0].min = new Date(DateUtils.getNextDay(validFrom));
        }
        // Clear Valid From Date
        else if (validFrom === null) {
            this.formData.fields.filter(f => f.name == "validTo")[0].disabled = true;
        }
        // Clear next field date
        this.formData.fields.filter(f => f.name == "validTo")[0].value = null;
	}

	/*
	update doc list dataset according to the chosen nationality
	 */
	checkNationalities() {
        this.formData.fields.filter(f => f.name == "typeCard")[0].dataset = [];

		let selectedNationalityId = this.formData.fields.filter(f => f.name == "nationality")[0].value;
		if(!ObjectUtils.isEmpty(selectedNationalityId)){
            let selectedCodeRegion = this.getCountryRegionCode(selectedNationalityId);
			if (selectedCodeRegion === 3) {
                this.formData.fields.filter(f => f.name == "typeCard")[0].dataset = this.foreignDocsList;
			}else{
                this.formData.fields.filter(f => f.name == "typeCard")[0].dataset = this.europeanDocsList;
			}
		}
	}

	getCountryRegionCode(countryId) {
        for (let key in this.nationalitiesList) {
            if (this.nationalitiesList[key].name === countryId) {
                return this.nationalitiesList[key].codeRegion;
            }
        }
	}

	/*
	Set max length for ID card field
	 */
    checkDocument() {
		// Set max valid length
		// For cni
		let typeCard = this.formData.fields.filter(f => f.name == "typeCard")[0].value;
		if (typeCard === '1') {
            this.formData.fields.filter(f => f.name == "cardNumber")[0].max = 12;
		}
		// For other types of docs
		else {
            this.formData.fields.filter(f => f.name == "cardNumber")[0].max = null;
		}
	}

	setMaxDeliveryDate() {
        let yesterday = new Date();
        yesterday.setDate(yesterday.getDate() - 1);

        this.formData.fields.filter(f => f.name == "deliveredDate")[0].max = yesterday;
	}

    savePersonalDetailsIdentity(){
        //if all fields are empty, ignore this step and go to the next page
		/*let ignoreThisStep = !ObjectUtils.isFormNotEmpty(this.formData.fields);
		if(ignoreThisStep){
			this.gotoBankSettings();
			return;
		}*/

        let prefectureValue = this.formData.fields.filter(f => f.name === "prefecture")[0].value;
        let nationalityValue = this.formData.fields.filter(f => f.name === "nationality")[0].value;
        let prefectureList = this.formData.fields.filter(f => f.name === "prefecture")[0].dataset;

		let identityCardData: any = {
			nationality: this.nationalitiesList.filter(f => f.name === nationalityValue)[0].gid.toString(),
			typeIDCard: this.formData.fields.filter(f => f.name === "typeCard")[0].value.toString(),
			numberIDCard: this.formData.fields.filter(f => f.name === "cardNumber")[0].value,
			deliveredDate: this.formData.fields.filter(f => f.name === "deliveredDate")[0].value,
			prefecture: prefectureList.filter(f => f.key === prefectureValue)[0].id.toString(),
			validFrom: this.formData.fields.filter(f => f.name === "validFrom")[0].value,
			validTo: this.formData.fields.filter(f => f.name === "validTo")[0].value
		};

		this.profileService.savePersonalDetailsIdentity(this.currentUser.id, identityCardData).then((data: StatusResponseDto) => {
			if (data.status == "success") {
				if (this.isInscription) {
					this.toast.presentEnteredToast();
					this.gotoBankSettings();
				} else {
					this.toast.presentEditingToast();
				}
			} else {
				this.toast.presentErrorToast();
				console.log(data.error);
			}
		});
	}

	gotoBankSettings(){
		this.navigationService.dismiss(this.viewCtrl);
		let vojPage: VojPage = {page: BankSettingsPage, isWebModal:true, params: {isInscription: this.isInscription}};
		this.navigationService.navigate("main", vojPage);
	}

	ngOnInit() {
		//load nationality list
		this.masterdataService.loadNationalitiesList().then((data: NationalityDto[]) => {
			this.nationalitiesList = data;

			//set dataset of nationalities of the form
			this.formData.fields.filter(f => f.name === "nationality")[0].dataset = this.masterdataService.convertNationalitiesListToDataset(data);
		});

		// Load Prefecture List
		this.masterdataService.loadPrefectureList().then((data: PrefectureDto[]) => {
			this.formData.fields[4].dataset = this.masterdataService.convertPrefectureListToDataset(data);
		});

		this.storage.get("currentUser").then((val) => {
			if (ObjectUtils.isEmpty(val)) {
				return;
			}
			this.currentUser = JSON.parse(val);

			this.initializationPersonalDetails(this.currentUser.id);
		});
	}
}


