import {Component} from '@angular/core';
import {NavParams, Platform, ViewController} from 'ionic-angular';

import {Configuration} from '../../../config/voj-configuration';
import {VojPageHeaderType} from '../../../models/voj-page-header.model';
import {VojConfiguration} from '../../../models/voj-configuration.model';
import {CurrentUserDto} from "../../../dtos/current-user.dto";
import {Storage} from "@ionic/storage";
import {BankDTO} from "../../../dtos/bank.dto";
import {ProfileService} from "../../../providers/profile-service/profile-service";
import {StatusResponseDto} from "../../../dtos/status-response.dto";
import {VojFormModel} from "../../../models/voj-form.model";
import {NavigationService} from "../../../services/navigation.service";
import {ToastService} from "../../../services/toast.service";
import {SearchPage} from "../../search-module/search/search";
import {VojPage} from "../../../models/voj-page";
import {BankConstraints} from "./bank-constraints";
import {ObjectUtils} from "../../../utils/utils";

@Component({
	selector: 'bank-settings-page',
	templateUrl: 'bank-settings.html',
})
export class BankSettingsPage{
	config: VojConfiguration;
	isInscription: boolean;
	currentUser: CurrentUserDto;

	formData: VojFormModel;
	headerData: VojPageHeaderType;

	savedBankData: BankDTO;
	currentUserName: string;

	constructor(public configurationService: Configuration,
				public platform: Platform,
				private navParams: NavParams,
				private storage: Storage,
				private profileService: ProfileService,
				private navigationService: NavigationService,
				private viewCtrl: ViewController,
				public toast: ToastService) {
		this.config = this.configurationService.configuration;
		this.isInscription = this.navParams.data.isInscription;

		this.headerData = {
			title: 'BANK_SETTINGS.TITLE',
			actions: [{
				id: 'help',
				label: 'help',
				icon: 'help',
				action: function () {
					console.log('button works')
				}
			}],
			isCanceled: true
		};

		this.formData = {
			fields: [
				{
					type: 'text',
					value: '',
					label: 'BANK_SETTINGS.BANK_NAME',
					visible: true,
					id: 'bankname',
					name: 'bankname',
					required: true,
					validationMsg: 'BANK_SETTINGS.BANK_NAME_ERR'
				},
				{
					type: 'text',
					label: 'BANK_SETTINGS.OWNER_NAME',
					value: '',
                    visible: true,
					readonly: true,
					id: 'ownername',
					name: 'ownername',
					required: true,
					validationMsg: 'BANK_SETTINGS.OWNER_NAME_ERR'
				},
				{
					type: 'text',
					label: 'BANK_SETTINGS.IBAN',
					value: '',
					visible: true,
					id: 'iban',
					name: 'iban',
					required: true,
					validationMsg: 'BANK_SETTINGS.IBAN_ERR',
					action: () => {
                        this.checkFields({iban: this.formData.fields.filter(f => f.name === 'iban')[0].value, bic: this.formData.fields.filter(f => f.name === 'bic')[0].value});
					}
				},
				{
					type: 'text',
					label: 'BANK_SETTINGS.BIC',
					value: '',
					visible: true,
					id: 'bic',
					name: 'bic',
					required: true,
					validationMsg: 'BANK_SETTINGS.BIC_ERR',
					action: () => {
                        this.checkFields({iban: this.formData.fields.filter(f => f.name === 'iban')[0].value, bic: this.formData.fields.filter(f => f.name === 'bic')[0].value});
					}
				}
			],
			submit: {
				id: 'submit',
				label: (this.isInscription ? 'BANK_SETTINGS.FOLLOWING_BTN' : 'BANK_SETTINGS.VALIDATE_BTN'),
				action: (event) => {
					this.saveBankData();
				}
			}
		}
	}

	ngOnInit() {
        this.storage.get("currentUser").then((val) => {
			if (ObjectUtils.isEmpty(val)) {
				return;
			}
			this.currentUser = JSON.parse(val);
			this.currentUserName = ((this.currentUser.identity.lastName.trim() + " " + this.currentUser.identity.middleName.trim()).trim() + " " + this.currentUser.identity.firstName.trim()).toUpperCase();

			//set owner bank account name: this name should be always the same as the current user name
			this.formData.fields.filter(f => f.name === "ownername")[0].value = this.currentUserName;
            this.initializationPersonalDetails(this.currentUser.id);
        });
	}

    initializationPersonalDetails(roleId) {
		this.profileService.getBankData(roleId).then((data: any) => {
			if(data.status && data.status != "success"){
				this.toast.presentErrorToast();
				console.log(data.error);
			}else {
				this.savedBankData = data;
				this.formData.fields.filter(f => f.name == "bankname")[0].value = this.savedBankData.bankName;
				this.formData.fields.filter(f => f.name == "iban")[0].value = this.savedBankData.iban;
				this.formData.fields.filter(f => f.name == "bic")[0].value = this.savedBankData.bic;
			}
		});
	}

	saveBankData() {
       //if all fields are empty, ignore this step and go to the next page
		/*let ignoreThisStep = !ObjectUtils.isFormNotEmpty(this.formData.fields);
		if(ignoreThisStep){
			this.navigationService.dismiss(this.viewCtrl);
			this.displaySuccessNotif();
			return;
		}*/
		let bankDataToSave: BankDTO = {
			id: this.savedBankData.id,
			bankName: this.formData.fields.filter(f => f.name === "bankname")[0].value.toUpperCase(),
			iban: this.formData.fields.filter(f => f.name === "iban")[0].value,
			bic: this.formData.fields.filter(f => f.name === "bic")[0].value,
			accountOwner: this.currentUserName.toUpperCase()
		};

		//if iban or bic are not valid, do not save data
		let isFormValid = this.checkFields(bankDataToSave);
		if(!isFormValid){
			return;
		}

		this.profileService.saveBankSetting(this.currentUser.id, bankDataToSave).then((data: StatusResponseDto) => {
			if (data.status == "success") {
				if (this.isInscription) {
                    let vojNav: VojPage = { page: SearchPage, isRoot: true };
                    this.config.isLargeScreen ? this.navigationService.dismiss(this.viewCtrl) : this.navigationService.navigate('main', vojNav);
					this.toast.presentWelcomeToast();
				} else {
					this.toast.presentEditingToast();
				}
			} else {
				this.toast.presentErrorToast();
				console.log(data.error);
			}
		});
	}

	/*
	check if iban and bic are valid, if they are not valid, display an error msg below the concerned field
	 */
    checkFields(bankData){
        if (bankData.iban || bankData.bic) {
            let isFormValid = true;

            let ibanId = this.formData.fields.filter(f => f.name === 'iban')[0].id;
            let bicId = this.formData.fields.filter(f => f.name === 'bic')[0].id;

            let hideIBANerror = true;
            let hideBICerror = true;

            if (!BankConstraints.isValidIban(bankData.iban)) {
                this.presentError(ibanId);
                hideIBANerror = false;
                isFormValid = false;
            }

            if (!BankConstraints.isValidBIC(bankData.bic)) {
                this.presentError(bicId);
                hideBICerror = false;
                isFormValid = false;
            }
            hideIBANerror === true ? this.hideError(ibanId) : this.presentError(ibanId);
            hideBICerror === true ? this.hideError(bicId) : this.presentError(bicId);

            return isFormValid;
        }
    }
    // Show validate msg
    presentError(parentId) {
		if (document.getElementById(parentId).getElementsByClassName('voj-validation-error-msg').length) {
            (document.getElementById(parentId).getElementsByClassName('voj-validation-error-msg')[0] as HTMLElement).removeAttribute('hidden');
            (document.getElementById(parentId).getElementsByClassName('voj-validation-error-msg')[0] as HTMLElement).style.display = 'block';
		}
    }
    // Hide validate msg
    hideError(parentId) {
        (document.getElementById(parentId).getElementsByClassName('voj-validation-error-msg')[0] as HTMLElement).style.display = 'none';
	}
}


