import {VojCalendarSlots, VojCalendarModel} from "../../../models/voj-calendar.model";
import {VojCalendarCompiler} from "./voj-calendar-compiler";
import {VojCalendarFactory} from "./voj-calendar-factory";
export class VojCalendarEngine {

    expression : string;
    slots : Array<VojCalendarSlots>;

    parse(expression : string) : VojCalendarModel {
        let data = JSON.parse(expression);
        let type = data.type;

        let factory : VojCalendarFactory = new VojCalendarFactory();
        let compiler : VojCalendarCompiler = factory.create(type);
        return compiler.parse(expression);
    }

    encode(model : VojCalendarModel) : string {
        let type = model.type;
        let factory : VojCalendarFactory = new VojCalendarFactory();
        let compiler : VojCalendarCompiler = factory.create(type);
        return compiler.encode(model);
    }

    display(model : VojCalendarModel) : Array<string> {
        let type = model.type;
        let factory : VojCalendarFactory = new VojCalendarFactory();
        let compiler : VojCalendarCompiler = factory.create(type);
        return compiler.display(model);
    }
}


